import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import CONFIG_BACKEND_MULTIPART_FORM from "../../component/util/api/configBackendOnlineShopMultipartForm";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, Typography } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import DropdownCategory from "../category/DropdownCategory.js";
import AlertText from "../../component/alert/AlertText.js";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


const ModalCreateUpdateProduct = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [isErrorCreateUpadate, setIsErrorCreateUpadate] = useState(false);
    const [messageErrorCreateUpdate, setMessageErrorCreateUpdate] = useState('');
    const [idCategory, setIdCategory] = useState(null);
    const [productName, setProductName] = useState('');
    const [productDescription, setProductDescription] = useState('');
    const [productImages, setProductImages] = useState('');
    const [price, setPrice] = useState(0);
    const [stock, setStock] = useState(0);
    const [discount, setDiscount] = useState(0);
    const [isNewImage, setIsNewImage] = useState(false);
    const [imagePreview, setImagePreview] = useState('/logo192.png');

    const handleCloseAll = () => {
        props.handleOpen();
        setIsLoading(false);
        resetAll();
        props.fetchListProductFromAnother();
    }

    const handleOnChangeProductImage = async (e) => {
        const file = e.target.files[0];
        setIsNewImage(true);
        setProductImages(file);
        setImagePreview(URL.createObjectURL(file));
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        var data = {
            idCategory: idCategory === '' ? props.productSelected.idCategory : idCategory,
            productName: productName === '' ? props.productSelected.productName : productName,
            productDescription: productDescription === '' ? props.productSelected.productDescription : productDescription,
            price: price === 0 ? props.productSelected.price : price,
            stock: stock === 0 ? props.productSelected.stock : stock,
            discount: discount === 0 ? props.productSelected.discount : discount,
        };

        // update image if image is new
        if (isNewImage) {
            data.file = productImages === '' ? props.productSelected.productImages : productImages
        }

        if (!props.productSelected.idProduct) {
            // create Product
            await CONFIG_BACKEND_MULTIPART_FORM.post(END_POINT_BACKEND_ONLINE_SHOP.PRODUCT, data
            ).then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    handleCloseAll();
                } else {
                    setIsErrorCreateUpadate(true);
                    setMessageErrorCreateUpdate(response.message);
                }
            }).catch((error) => {
                console.log(error);
            });
        } else {
            // update Product
            await CONFIG_BACKEND_MULTIPART_FORM.put(END_POINT_BACKEND_ONLINE_SHOP.PRODUCT + '/' + props.productSelected.idProduct, data
            ).then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    handleCloseAll();
                } else {
                    setIsErrorCreateUpadate(true);
                    setMessageErrorCreateUpdate(response.message);
                }
            }).catch((error) => {
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
                console.log(error);
            });
        }
    }

    const handleCancelModal = async () => {
        props.handleOpen();
        resetAll();
    }

    const resetAll = () => {
        setIdCategory(null);
        setProductName('');
        setProductDescription('');
        setProductImages('');
        setPrice(0);
        setStock(0);
        setImagePreview('');
        setIsNewImage(false);
        setIsLoading(false);
    }

    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.isOpen}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    {!props.productSelected.idProduct ? 'Tambah Produk' : 'Edit Produk'}
                    {isLoading && <ProgressLinear />}
                    {isErrorCreateUpadate && <AlertText status={false} message={messageErrorCreateUpdate} />}
                </DialogTitle>
                <DialogContent>
                    <form onSubmit={handleSubmit}>
                        <Grid
                            container
                            spacing={2}
                            direction="column"
                            justifyContent="flex-start"
                            alignItems="stretch"
                            marginTop="5px"
                        >

                            <Grid item>
                                <DropdownCategory
                                    categorySelected={props.productSelected.categorie ? props.productSelected.categorie : {}}
                                    handleSetIdCategory={(idCategory) => setIdCategory(idCategory)}
                                />
                            </Grid>

                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.productSelected.productName}
                                    size="small"
                                    onChange={(e) => setProductName(e.target.value)}
                                    label="Nama Produk"
                                    variant="outlined"
                                    required />
                            </Grid>

                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.productSelected.productDescription}
                                    size="small"
                                    onChange={(e) => setProductDescription(e.target.value)}
                                    label="Deskripsi Produk"
                                    variant="outlined"
                                    required />
                            </Grid>

                            <Grid item>
                                <Grid
                                    container
                                    direction="row"
                                    justifyContent="space-between"
                                    alignItems="flex-start"
                                    columnSpacing={{ xs: 1, sm: 2, md: 3 }}
                                >
                                    {/* PRICE */}
                                    <Grid item xs={6}>
                                        <TextField
                                            fullWidth
                                            defaultValue={props.productSelected.price}
                                            size="small"
                                            type="number"
                                            onChange={(e) => setPrice(e.target.value)}
                                            label="Harga Produk"
                                            variant="outlined"
                                            required />
                                    </Grid>

                                    {/* STOCK */}
                                    <Grid item xs={3}>
                                        <TextField
                                            fullWidth
                                            defaultValue={props.productSelected.stock}
                                            size="small"
                                            type="number"
                                            onChange={(e) => setStock(e.target.value)}
                                            label="Stok Produk"
                                            variant="outlined"
                                            disabled={!props.productSelected.idProduct ? false : true}
                                            required />
                                    </Grid>

                                    {/* DISCOUNT */}
                                    <Grid item xs={3}>
                                        <TextField
                                            fullWidth
                                            defaultValue={props.productSelected.discount}
                                            size="small"
                                            type="number"
                                            onChange={(e) => setDiscount(e.target.value)}
                                            label="Discount"
                                            variant="outlined"
                                            required />
                                    </Grid>



                                </Grid>
                            </Grid>

                            <Grid item>
                                {
                                    isNewImage ?
                                        <img src={imagePreview} width="250px" />
                                        :
                                        props.productSelected.productImages &&
                                        <img src={process.env.REACT_APP_HOST_URL_IMAGE + props.productSelected.productImages} width="250px" />
                                }
                            </Grid>

                            <Grid item>
                                <input
                                    type={'file'}
                                    onChange={(e) => handleOnChangeProductImage(e)}
                                    required={props.productSelected.productImages ? false : true} />
                            </Grid>
                        </Grid>
                        <DialogActions>
                            <Button onClick={handleCancelModal}>Cancel</Button>
                            <Button type="submit"> Save</Button>
                        </DialogActions>
                    </form>
                </DialogContent>


            </Dialog>
        </div>
    );

}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalCreateUpdateProduct);