import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert, getUserProfil } from "../../component/util/api/action";
import TruncateText from "../../component/template/TruncateText";

import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton, Grid } from "@mui/material";
import TablePagination from '@mui/material/TablePagination';
import HeaderTable from "../../component/template/HeaderTable";
import ModalDelete from "../../component/modal/ModalDelete";
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import ModalCreateUpdateMember from "./ModalCreateUpdateMember";

const ListMember = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpenDelete, setIsOpenDelete] = useState(false);
    const [isOpenModalSetPassword, setIsOpenUpdatePassword] = useState(false);
    const [isFetching, setIsFetching] = useState(true);
    const [size, setSize] = useState(10);
    const [pages, setPages] = useState(0);
    const [totalPage, setTotalPage] = useState(0);
    const [totalItem, setTotalItem] = useState(0);
    const [itemSearch, setItemSearch] = useState('');

    const [listMember, setListMember] = useState([]);
    const [memberSelected, setMemberSelected] = useState({});
    const [idMember, setIdMember] = useState('');

    useEffect(() => {
        const getDatas = async () => {
            fetchListMember(size, pages, '');
            props.getUserProfil();
        };

        getDatas();

    }, []);

    const fetchListMember = async (size, pages, itemSearch) => {
        setIsFetching(true);

        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.MEMBER
            + "?size=" + size
            + "&page=" + pages
            + "&q=" + itemSearch
        ).then((response) => {
            if (response.status) {
                setPages(response.data.currentPage);
                setTotalPage(response.data.totalPages);
                setTotalItem(response.data.totalItems);
                setListMember(response.data.content);
            } else {
                setSize(10);
                setPages(0);
                setTotalPage(0);
                setTotalItem(0);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: response.message
                })
            }

            setIsFetching(false);
        }).catch((error) => {
            console.log(error);
        });
    }

    const handleSearch = async (search) => {
        setItemSearch(search);
        await fetchListMember(size, 0, search);
    }

    const handleOpen = () => {
        setIsOpen(!isOpen);
        if (isOpen) {
            setMemberSelected({});
        }
    }

    const handleDeleteOpen = async (member) => {
        setMemberSelected(member);
        setIdMember(member.idMember);
        handleCloseDelete();
    }

    const handleCloseDelete = () => {
        setIsOpenDelete(!isOpenDelete);
    }

    const handleUpdate = (admin) => {
        setMemberSelected(admin);
        handleOpen();
    }

    const fetchListMemberFromAnother = async () => {
        await fetchListMember(size, pages, itemSearch);
    }

    const handleDoDelete = async () => {
        await CONFIG_BACKEND.delete(END_POINT_BACKEND_ONLINE_SHOP.MEMBER + "/" + idMember)
            .then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    fetchListMemberFromAnother();
                    handleCloseDelete();
                }
            })
            .catch((error) => {
                console.log(error);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
            });
    }

    const handleChangePage = async (e, newPage) => {
        setPages(newPage);
        await fetchListMember(size, newPage, itemSearch);
    }

    const handleChangeRowsPerPage = async (e) => {
        const sizeNow = parseInt(e.target.value, 10);
        setSize(sizeNow);
        setPages(0);
        await fetchListMember(sizeNow, pages, itemSearch);
    }

    return (
        <Fragment>
            <ModalCreateUpdateMember isOpen={isOpen} handleOpen={handleOpen} fetchListMemberFromAnother={fetchListMemberFromAnother} memberSelected={memberSelected} />
            <ModalDelete isOpenDelete={isOpenDelete} handleCloseDelete={handleCloseDelete} nameOfObject={memberSelected.username} handleDoDelete={handleDoDelete} /> 
            <HeaderTable handleSearch={handleSearch} placeholder="Cari Member" handleOpen={handleOpen} />

            {isFetching ? <LoadingSkeletonTable /> :
                <TableContainer component={Paper}>
                    <Table size="small" aria-label="collapsible table">
                        <TableHead>
                            <TableRow key="header">
                                <TableCell align="left">No</TableCell>
                                <TableCell align="left">Kode Member</TableCell>
                                <TableCell align="left">Username</TableCell>
                                <TableCell align="left">Nama</TableCell>
                                <TableCell align="center">#</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {listMember.length > 0 &&
                                listMember.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell align="left">{index + 1}</TableCell>
                                        <TableCell align="left">{row.memberCode} </TableCell>
                                        <TableCell align="left">{row.username}</TableCell>
                                        <TableCell align="left">{row.memberName}</TableCell>
                                        <TableCell align="center">
                                             <IconButton color="secondary" onClick={() => handleUpdate(row)} title={" Update " + row.username}>
                                                <EditOutlinedIcon />
                                            </IconButton >
                                            <IconButton color="error" onClick={() => handleDeleteOpen(row)} title={" Delete " + row.username} disabled={props.userProfil.idAdmin === row.idAdmin ? true : false} >
                                                <DeleteOutlineOutlinedIcon />
                                            </IconButton >
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        labelRowsPerPage=""
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={totalItem}
                        rowsPerPage={size}
                        page={pages}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </TableContainer>
            }
        </Fragment>
    )

}



const mapStateToProps = state => {
    return {
        userProfil: state.userProfil
    }
}

export default connect(mapStateToProps, { handleAlert, getUserProfil })(ListMember);