import React, { useEffect, useState, Fragment } from 'react';
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";

import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import CircularProgress from '@mui/material/CircularProgress';

function sleep(delay = 0) {
    return new Promise((resolve) => {
        setTimeout(resolve, delay);
    });
}

const DropdownInformation = (props) => {
    const [isFetching, setIsFetching] = useState(false);
    const [open, setOpen] = useState(false);
    const [options, setOptions] = useState([]);

    useEffect(() => {
        if (props.informationDetailSelected.idInformation === '') {
            handleOnChange('');
        }

    }, [props.informationDetailSelected.idInformation])

    const handleOnChange = async (value = null) => {
        setIsFetching(true);

        const informationSearch = value ? '?informationSearch=' + value : '';
        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION_DROPDOWN + informationSearch)
            .then((response) => {
                setIsFetching(false);
                if (response.status) {
                    setOptions(response.data);
                } else {
                    setOptions([]);
                }
            }).catch((error) => {
                console.log(error);
            })

    }

    const handleSetIdInformation = async (value) => {
        try {
            props.handleSetIdInformation(value.idInformation);
        } catch (error) {
            props.handleSetIdInformation('');
        }
    }

    return (
        <Fragment>
            <Autocomplete
                sx={{ width: 300 }}
                open={open}
                onOpen={() => {
                    setOpen(true);
                    handleOnChange('');
                }}
                onClose={() => {
                    setOpen(false);
                }}
                defaultValue={props.informationDetailSelected.idInformation ? props.informationDetailSelected : null}
                isOptionEqualToValue={(option, value) => option.informationTitle === value.informationTitle}
                getOptionLabel={(option) => option.informationTitle}
                options={options.length > 0 ? options :
                    props.informationDetailSelected.idInformation ? [props.informationDetailSelected] : []}
                onChange={(event, value) => handleSetIdInformation(value)}
                loading={isFetching}
                renderInput={(params) => (
                    <TextField
                        {...params}
                        label="Cari Informasi"
                        size='small'
                        onChange={(e) => handleOnChange(e.target.value)}
                        InputProps={{
                            ...params.InputProps,
                            endAdornment: (
                                <Fragment>
                                    {isFetching ? <CircularProgress color="inherit" size={20} /> : null}
                                    {params.InputProps.endAdornment}
                                </Fragment>
                            ),
                        }}
                    />
                )}
            />
        </Fragment>
    );
}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps)(DropdownInformation);