import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import Slide from "@mui/material/Slide";
import Textarea from "@mui/joy/Textarea";
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, Typography } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import AlertText from "../../component/alert/AlertText";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const ModalCreateUpdateMember = (props) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isErrorCreateUpadate, setIsErrorCreateUpadate] = useState(false);
  const [messageErrorCreateUpdate, setMessageErrorCreateUpdate] = useState("");

  const [idMember, setIdMember] = useState(null);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [memberName, setMemberName] = useState("");
  const [address, setAddress] = useState("");
  const [email, setEmail] = useState("");

  useEffect(() => {
    if (props.isOpen) {
      setAddress(props.memberSelected.address);
    }
  }, [props.isOpen]);

  const handleCancelModal = async () => {
    props.handleOpen();
    resetAll();
  };

  const handleCloseAll = () => {
    props.handleOpen();
    setIsLoading(false);
    resetAll();
    props.fetchListMemberFromAnother();
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setIsErrorCreateUpadate(false);

    var data = {
      idMember: idMember === null ? props.memberSelected.idMember : idMember,
      username: username === "" ? props.memberSelected.username : username,
      password: password === "" ? props.memberSelected.password : password,
      memberName: memberName === "" ? props.memberSelected.memberName : memberName,
      address: address === "" ? props.memberSelected.address : address,
      email: email === "" ? props.memberSelected.email : email,
    };

    if (!props.memberSelected.idMember) {
      // create Member
      await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.MEMBER, data)
        .then((response) => {
          if (response.status) {
            props.handleAlert({
              isOpen: true,
              title: "success",
              message: response.message,
            });
            handleCloseAll();
          } else {
            setIsLoading(false);
            setIsErrorCreateUpadate(true);
            setMessageErrorCreateUpdate(response.message);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      // update Member
      await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.MEMBER + "/" + props.memberSelected.idMember, data)
        .then((response) => {
          if (response.status) {
            props.handleAlert({
              isOpen: true,
              title: "success",
              message: response.message,
            });
            handleCloseAll();
          } else {
            setIsLoading(false);
            setIsErrorCreateUpadate(true);
            setMessageErrorCreateUpdate(response.message);
          }
        })
        .catch((error) => {
          props.handleAlert({
            isOpen: true,
            title: "error",
            message: error.message,
          });
          console.log(error);
        });
    }
  };

  const resetAll = () => {
    setIsErrorCreateUpadate(false);
    setIdMember(null);
    setUsername("");
    setPassword("");
    setMemberName("");
    setAddress("");
    setEmail("");
  };

  return (
    <div>
      <Dialog fullWidth={true} maxWidth="sm" open={props.isOpen} TransitionComponent={Transition}>
        <DialogTitle>
          {!props.memberSelected.idMember ? "Tambah Member" : "Edit Member"}
          {isLoading && <ProgressLinear />}
          {isErrorCreateUpadate && <AlertText status={false} message={messageErrorCreateUpdate} />}
        </DialogTitle>
        <DialogContent>
          <form onSubmit={handleSubmit}>
            <Grid container spacing={2} direction="column" justifyContent="flex-start" alignItems="stretch" marginTop="5px">
              {props.memberSelected.idMember && (
                <Grid item>
                  <TextField fullWidth defaultValue={props.memberSelected.memberCode} size="small" label="Kode Member" variant="outlined" disabled required />
                </Grid>
              )}
              <Grid item>
                <TextField
                  fullWidth
                  defaultValue={props.memberSelected.username}
                  size="small"
                  onChange={(e) => setUsername(e.target.value)}
                  label="Username"
                  variant="outlined"
                  disabled={props.memberSelected.idMember ? true : false}
                  required
                />
              </Grid>
              <Grid item>
                <TextField fullWidth defaultValue={props.memberSelected.memberName} size="small" onChange={(e) => setMemberName(e.target.value)} label="Nama Member" variant="outlined" required />
              </Grid>
              <Grid item>
                <TextField fullWidth defaultValue={props.memberSelected.email} size="small" onChange={(e) => setEmail(e.target.value)} label="Email" variant="outlined" type="email" required />
              </Grid>
              <Grid item>
                <Textarea placeholder="Alamat" value={address} onChange={(e) => setAddress(e.target.value)} minRows={2} maxRows={4} required />
              </Grid>
            </Grid>
            <DialogActions>
              <Button onClick={handleCancelModal}>Cancel</Button>
              <Button type="submit"> Save</Button>
            </DialogActions>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    alertNotificationEntity: state.alertNotificationEntity,
  };
};
export default connect(mapStateToProps, { handleAlert })(ModalCreateUpdateMember);
