import axios from "axios";
// import history from "../history";
import { END_POINT_BACKEND_ONLINE_SHOP } from "./endpointBackendOnlineShop";

const instance = axios.create({
    baseURL: END_POINT_BACKEND_ONLINE_SHOP.ONLINE_SHOP_HOST,
    headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
    }
});


instance.defaults.withCredentials = false;
instance.interceptors.response.use(response => {
    console.groupCollapsed(" ============== Response Login ==============");
    console.info('status', response.status);
    console.info('data', response.data);
    console.groupEnd();
    if (response.status) {
        localStorage.setItem("userProfil", JSON.stringify(response.data.data));
        localStorage.setItem("token", response.data.data.accessToken);
    }

    return response.data;

}, error => {
    console.groupCollapsed(" ============== Response Login Error==============");
    console.info('error', error);
    console.info('error.response', error.response);
    console.groupEnd();

    return error.response.data;
});

export default instance;