import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { getUserProfil } from "../util/api/action";

class Sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      location: window.location.pathname,
    };
  }

  async componentDidMount() {
    await this.props.getUserProfil();
  }

  render() {
    const { location } = this.state;
    return (
      <aside id="sidebar" className="sidebar">
        <ul className="sidebar-nav" id="sidebar-nav">
          {/* <li class="nav-item">
                        <Link to="/" className={location === '/' ? 'nav-link' : 'nav-link collapsed'}>
                            <i class="bi bi-box-arrow-in-right"></i>
                            <span>Login</span>
                        </Link>
                    </li> */}

          <li className="nav-item">
            <Link to="/home" className={location === "/home" ? "nav-link" : "nav-link collapsed"}>
              <i className="bi bi-grid"></i>
              <span>Home</span>
            </Link>
          </li>

          <li className="nav-item">
            <Link to="/category" className={location === "/category" ? "nav-link" : "nav-link collapsed"}>
              <i className="bi bi-grid"></i>
              <span>Kategori</span>
            </Link>
          </li>

          <li className="nav-item">
            <Link to="/product" className={location === "/product" ? "nav-link" : "nav-link collapsed"}>
              <i className="bi bi-grid"></i>
              <span>Produk</span>
            </Link>
          </li>

          <li className="nav-item">
            <Link to="/payment" className={location === "/payment" ? "nav-link" : "nav-link collapsed"}>
              <i className="bi bi-grid"></i>
              <span>Pembayaran</span>
            </Link>
          </li>
          {
            /* Just idRole 1 for access this sidebar */
            this.props.userProfil.idRole === 1 && (
              <li className="nav-item">
                <a className={location.includes("/user-") ? "nav-link" : "nav-link collapsed"} data-bs-target="#member-nav" data-bs-toggle="collapse" href="#">
                  <i className="bi bi-menu-button-wide"></i>
                  <span>User</span>
                  <i className="bi bi-chevron-down ms-auto"></i>
                </a>
                <ul id="member-nav" className={location.includes("/user-") ? "nav-content collapse show" : "nav-content collapsed collapse"} data-bs-parent="#sidebar-nav">
                  <li>
                    <Link to="/user-admin" className={location === "/user-admin" ? "nav-link" : "nav-link collapsed"}>
                      <i className="bi bi-circle"></i>
                      <span>Admin</span>
                    </Link>
                  </li>

                  <li>
                    <Link to="/user-member" className={location === "/user-member" ? "nav-link" : "nav-link collapsed"}>
                      <i className="bi bi-circle"></i>
                      <span>Member</span>
                    </Link>
                  </li>
                </ul>
              </li>
            )
          }
          <li className="nav-item">
            <Link to="/transaction" className={location === "/transaction" ? "nav-link" : "nav-link collapsed"}>
              <i className="bi bi-cart4"></i>
              <span>Transaksi</span>
            </Link>
          </li>

          <li className="nav-item">
            <Link to="/information" className={location === "/information" ? "nav-link" : "nav-link collapsed"}>
              <i className="bi bi-info-square"></i>
              <span>Informasi</span>
            </Link>
          </li>

          <li className="nav-item">
            <Link to="/about" className={location === "/about" ? "nav-link" : "nav-link collapsed"}>
              <i className="bi bi-envelope"></i>
              <span>About</span>
            </Link>
          </li>

          {/*
                    <li className="nav-item">
                        <a className="nav-link collapsed" data-bs-target="#forms-nav" data-bs-toggle="collapse" href="#">
                            <i className="bi bi-journal-text"></i><span>Forms</span><i className="bi bi-chevron-down ms-auto"></i>
                        </a>
                        <ul id="forms-nav" className="nav-content collapse " data-bs-parent="#sidebar-nav">
                            <li>
                                <a href="forms-elements.html">
                                    <i className="bi bi-circle"></i><span>Form Elements</span>
                                </a>
                            </li>
                            <li>
                                <a href="forms-layouts.html">
                                    <i className="bi bi-circle"></i><span>Form Layouts</span>
                                </a>
                            </li>
                            <li>
                                <a href="forms-editors.html">
                                    <i className="bi bi-circle"></i><span>Form Editors</span>
                                </a>
                            </li>
                            <li>
                                <a href="forms-validation.html">
                                    <i className="bi bi-circle"></i><span>Form Validation</span>
                                </a>
                            </li>
                        </ul>
                    </li> */}
        </ul>
      </aside>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    userProfil: state.userProfil,
  };
};

export default connect(mapStateToProps, { getUserProfil })(Sidebar);
