import React from "react";
import { connect } from "react-redux";
import actionType from "../../redux/reducer/globalActionType";


const alertNotification = (props) => {
    if (props.alertNotificationEntity.isOpen) {
        return (
            <div class="alert alert-danger alert-dismissible fade show" role="alert">
                <strong>{props.alertNotificationEntity.title}</strong> {props.alertNotificationEntity.message}
                <button type="button" class="btn-close" onClick={() => props.handleLoginError({ isOpen: false })} aria-label="Close"></button>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        handleLoginError: (payload) => dispatch({ type: actionType.ALERT_ACTION, alertNotificationEntity: payload }),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(alertNotification);
