import React from "react";
import { Button } from "@mui/material";


export default function SpanStatusAdmin(props) {
    if (props.status === 1) {
        return (
            <span style={{
                background: '#00d50ad4',
                color: 'white',
                padding: '6px',
                borderRadius: '9px'
            }}>aktif</span>
        );
    } else if (props.status === 2) {
        return (
            <span style={{
                background: '#ff9b00d4',
                color: 'white',
                padding: '6px',
                borderRadius: '9px'
            }}>suspend</span>
        );
    }

}