import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert, getUserProfil } from "../../component/util/api/action";
import TruncateText from "../../component/template/TruncateText";

import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton, Checkbox } from "@mui/material";
import TablePagination from "@mui/material/TablePagination";
import HeaderTable from "../../component/template/HeaderTable";
import ModalDelete from "../../component/modal/ModalDelete";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import DeleteOutlineOutlinedIcon from "@mui/icons-material/DeleteOutlineOutlined";
import FormatListNumberedIcon from '@mui/icons-material/FormatListNumbered';
import ModalCreateUpdateInformation from "./ModalCreateUpdateInformation";

const ListInformation = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isOpenDelete, setIsOpenDelete] = useState(false);
  const [isFetching, setIsFetching] = useState(true);
  const [size, setSize] = useState(10);
  const [pages, setPages] = useState(0);
  const [totalPage, setTotalPage] = useState(0);
  const [totalItem, setTotalItem] = useState(0);
  const [itemSearch, setItemSearch] = useState("");

  const [listInformation, setListInformation] = useState([]);
  const [informationSelected, setInformationSelected] = useState({});
  const [idInformation, setIdInformation] = useState("");

  useEffect(() => {
    const getDatas = async () => {
      fetchListInformation(size, pages, "");
    };

    getDatas();
  }, []);

  const fetchListInformation = async (size, pages, itemSearch) => {
    setIsFetching(true);

    await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION + "?size=" + size + "&page=" + pages + "&q=" + itemSearch)
      .then((response) => {
        if (response.status) {
          setPages(response.data.currentPage);
          setTotalPage(response.data.totalPages);
          setTotalItem(response.data.totalItems);
          setListInformation(response.data.content);
        } else {
          setSize(10);
          setPages(0);
          setTotalPage(0);
          setTotalItem(0);
          props.handleAlert({
            isOpen: true,
            title: "error",
            message: response.message,
          });
        }

        setIsFetching(false);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const handleSearch = async (search) => {
    setItemSearch(search);
    await fetchListInformation(size, 0, search);
  };

  const handleOpen = () => {
    setIsOpen(!isOpen);
    if (isOpen) {
      setInformationSelected({});
    }
  };

  const handleDeleteOpen = async (information) => {
    setInformationSelected(information);
    setIdInformation(information.idInformation);
    handleCloseDelete();
  };

  const handleCloseDelete = () => {
    setIsOpenDelete(!isOpenDelete);
  };

  const handleUpdate = (information) => {
    setInformationSelected(information);
    handleOpen();
  };

  const handleDoDelete = async () => {
    await CONFIG_BACKEND.delete(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION + "/" + idInformation)
      .then((response) => {
        if (response.status) {
          props.handleAlert({
            isOpen: true,
            title: 'success',
            message: response.message
          });
          fetchListInformationFromAnother();
          handleCloseDelete();
        }
      })
      .catch((error) => {
        console.log(error);
        props.handleAlert({
          isOpen: true,
          title: 'error',
          message: error.message
        });
      });
  }

  const fetchListInformationFromAnother = async () => {
    await fetchListInformation(size, pages, itemSearch);
  };

  const handleChangePage = async (e, newPage) => {
    setPages(newPage);
    await fetchListInformation(size, newPage, itemSearch);
  };

  const handleChangeRowsPerPage = async (e) => {
    const sizeNow = parseInt(e.target.value, 10);
    setSize(sizeNow);
    setPages(0);
    await fetchListInformation(sizeNow, pages, itemSearch);
  };

  const handleChangeCheckboxIsDisplay = async (idInformation, isDisplay) => {
    setIsFetching(true);
    let data = {
      isDisplay: isDisplay,
    };

    await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION + "/" + idInformation, data)
      .then((response) => {
        if (response.status) {
          props.handleAlert({
            isOpen: true,
            title: "success",
            message: response.message,
          });
          fetchListInformationFromAnother();
        }
      })
      .catch((error) => {
        props.handleAlert({
          isOpen: true,
          title: "error",
          message: error.message,
        });
        console.log(error);
      });
  };

  return (
    <Fragment>
      <ModalCreateUpdateInformation isOpen={isOpen} handleOpen={handleOpen} fetchListInformationFromAnother={fetchListInformationFromAnother} informationSelected={informationSelected} />
      <ModalDelete isOpenDelete={isOpenDelete} handleCloseDelete={handleCloseDelete} nameOfObject={informationSelected.informationTitle} handleDoDelete={handleDoDelete} />
      <HeaderTable handleSearch={handleSearch} placeholder="Cari Informasi" handleOpen={handleOpen} />

      {isFetching ? (
        <LoadingSkeletonTable />
      ) : (
        <TableContainer component={Paper}>
          <Table size="small" aria-label="collapsible table">
            <TableHead>
              <TableRow key="header">
                <TableCell align="left">Kode</TableCell>
                <TableCell align="left">Judul</TableCell>
                <TableCell align="left">Deskripsi</TableCell>
                <TableCell align="left">Start/End</TableCell>
                <TableCell align="left">Tampil</TableCell>
                <TableCell align="center">#</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {listInformation.length > 0 &&
                listInformation.map((row, index) => (
                  <TableRow key={row.id}>
                    <TableCell align="left">{row.informationCode} </TableCell>
                    <TableCell align="left">
                      <TruncateText text={row.informationTitle} />
                    </TableCell>
                    <TableCell align="left">
                      <TruncateText text={row.informationDescription} />
                    </TableCell>
                    <TableCell align="left">{row.displayStartDate ? row.displayStartDateString : "-"} s/d {row.displayEndDate ? row.displayEndDateString : "-"}</TableCell>
                    <TableCell align="left">
                      <Checkbox size="small" checked={row.isDisplay} onClick={() => handleChangeCheckboxIsDisplay(row.idInformation, !row.isDisplay)} />
                    </TableCell>
                    <TableCell align="center">
                      <IconButton color="secondary" onClick={() => handleUpdate(row)} title={" Update " + row.informationTitle}>
                        <EditOutlinedIcon />
                      </IconButton>
                      <IconButton color="error" onClick={() => handleDeleteOpen(row)} title={" Delete " + row.username}>
                        <DeleteOutlineOutlinedIcon />
                      </IconButton>
                      <IconButton title={" Detail " + row.informationTitle}>
                        <Link to={"/informationDetail?idInformation=" +row.idInformation} target="_blank" ><FormatListNumberedIcon /></Link>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
          <TablePagination
            labelRowsPerPage=""
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={totalItem}
            rowsPerPage={size}
            page={pages}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </TableContainer>
      )}
    </Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    userProfil: state.userProfil,
  };
};

export default connect(mapStateToProps, { handleAlert, getUserProfil })(ListInformation);
