import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DateTimePicker } from "@mui/x-date-pickers/DateTimePicker";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import Slide from "@mui/material/Slide";
import Textarea from "@mui/joy/Textarea";
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, FormControlLabel, Checkbox } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import AlertText from "../../component/alert/AlertText";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const ModalCreateUpdateInformation = (props) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isErrorCreateUpadate, setIsErrorCreateUpadate] = useState(false);
  const [messageErrorCreateUpdate, setMessageErrorCreateUpdate] = useState("");

  const [idInformation, setIdInformation] = useState(null);
  const [informationCode, setInformationCode] = useState("");
  const [informationTitle, setInformationTitle] = useState("");
  const [informationDescription, setInformationDescription] = useState("");
  const [isDisplay, setIsDisplay] = useState(false);
  const [displayStartDate, setDisplayStartDate] = useState("");
  const [displayEndDate, setDisplayEndDate] = useState("");

  useEffect(() => {
    if (props.isOpen) {
      setInformationDescription(props.informationSelected.informationDescription);
      setDisplayStartDate(props.informationSelected.displayStartDate);
      setDisplayEndDate(props.informationSelected.displayEndDate);
    }
  }, [props.isOpen]);

  const handleCancelModal = async () => {
    props.handleOpen();
    resetAll();
  };

  const handleCloseAll = () => {
    props.handleOpen();
    setIsLoading(false);
    resetAll();
    props.fetchListInformationFromAnother();
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setIsErrorCreateUpadate(false);

    var data = {
      idInformation: idInformation ? idInformation : props.informationSelected.idInformation,
      informationCode: informationCode ? informationCode : props.informationSelected.informationCode,
      informationTitle: informationTitle ? informationTitle : props.informationSelected.informationTitle,
      informationDescription: informationDescription ? informationDescription : props.informationSelected.informationDescription,
      isDisplay: isDisplay ? isDisplay : props.informationSelected.isDisplay,
      displayStartDate: displayStartDate ? displayStartDate : props.informationSelected.displayStartDate,
      displayEndDate: displayEndDate ? displayEndDate : props.informationSelected.displayEndDate,
    };

    if (!props.informationSelected.idInformation) {
      // create Information
      await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION, data)
        .then((response) => {
          if (response.status) {
            props.handleAlert({
              isOpen: true,
              title: "success",
              message: response.message,
            });
            handleCloseAll();
          } else {
            setIsLoading(false);
            setIsErrorCreateUpadate(true);
            setMessageErrorCreateUpdate(response.message);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      // update Information
      await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION + "/" + props.informationSelected.idInformation, data)
        .then((response) => {
          if (response.status) {
            props.handleAlert({
              isOpen: true,
              title: "success",
              message: response.message,
            });
            handleCloseAll();
          } else {
            setIsLoading(false);
            setIsErrorCreateUpadate(true);
            setMessageErrorCreateUpdate(response.message);
          }
        })
        .catch((error) => {
          props.handleAlert({
            isOpen: true,
            title: "error",
            message: error.message,
          });
          console.log(error);
        });
    }
  };

  const resetAll = () => {
    setIsErrorCreateUpadate(false);
    setIdInformation(null);
    setInformationCode("");
    setInformationTitle("");
    setInformationDescription("");
    setIsDisplay(false);
    setDisplayStartDate("");
    setDisplayEndDate("");
  };

  return (
    <div>
      <Dialog fullWidth={true} maxWidth="sm" open={props.isOpen} TransitionComponent={Transition}>
        <DialogTitle>
          {!props.informationSelected.idInformation ? "Tambah Informasi" : "Edit Informasi"}
          {isLoading && <ProgressLinear />}
          {isErrorCreateUpadate && <AlertText status={false} message={messageErrorCreateUpdate} />}
        </DialogTitle>
        <DialogContent>
          <form onSubmit={handleSubmit}>
            <Grid container spacing={2} direction="column" justifyContent="flex-start" alignItems="stretch" marginTop="5px">
              <Grid item>
                <TextField
                  fullWidth
                  defaultValue={props.informationSelected.informationCode}
                  size="small"
                  onChange={(e) => setInformationCode(e.target.value)}
                  label="Kode Informasi"
                  variant="outlined"
                  required
                />
              </Grid>
              <Grid item>
                <TextField
                  fullWidth
                  defaultValue={props.informationSelected.informationTitle}
                  size="small"
                  onChange={(e) => setInformationTitle(e.target.value)}
                  label="Judul Informasi"
                  variant="outlined"
                  required
                />
              </Grid>
              <Grid item>

                <Grid container spacing={2} >
                  <Grid item><LocalizationProvider dateAdapter={AdapterDateFns}>
                    <DateTimePicker
                      label="Start Date"
                      ampm={false}
                      size="small"
                      renderInput={(params) => <TextField {...params} />}
                      value={displayStartDate}
                      onChange={(e) => {
                        setDisplayStartDate(e);
                      }}
                    />
                  </LocalizationProvider>
                  </Grid>
                  <Grid item>
                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                      <DateTimePicker
                        label="End Date"
                        ampm={false}
                        size="small"
                        renderInput={(params) => <TextField {...params} />}
                        value={displayEndDate}
                        onChange={(e) => {
                          setDisplayEndDate(e);
                        }}
                      />
                    </LocalizationProvider>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item>
                <Textarea placeholder="Deskripsi Informasi" value={informationDescription} onChange={(e) => setInformationDescription(e.target.value)} minRows={2} maxRows={4} required />
              </Grid>
            </Grid>
            <DialogActions>
              <Button onClick={handleCancelModal}>Cancel</Button>
              <Button type="submit"> Save</Button>
            </DialogActions>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    alertNotificationEntity: state.alertNotificationEntity,
  };
};
export default connect(mapStateToProps, { handleAlert })(ModalCreateUpdateInformation);
