import React, { useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND_MULTIPART_FORM from "../../component/util/api/configBackendOnlineShopMultipartForm";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid} from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import AlertText from "../../component/alert/AlertText.js";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


const ModalImportUpdateStock = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [isErrorCreateUpadate, setIsErrorCreateUpadate] = useState(false);
    const [messageErrorCreateUpdate, setMessageErrorCreateUpdate] = useState('');
    const [fileUpload, setFileUpload] = useState('');

    const handleCloseAll = () => {
        setIsLoading(false);
        setFileUpload('');
        props.handleOpenImportStock();
        props.fetchListProductFromAnother();
    }

    const handleChangeFileUploadExcell = (e) => {
        const file = e.target.files[0];
        setFileUpload(file);
    }

    const handleSubmitUploadExcell = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        await CONFIG_BACKEND_MULTIPART_FORM.post(END_POINT_BACKEND_ONLINE_SHOP.PRODUCT_IMPORT_UPDATE_STOCK, {
            file : fileUpload
        }
        ).then((response) => {
            if (response.status) {
                props.handleAlert({
                    isOpen: true,
                    title: 'success',
                    message: response.message
                });
                handleCloseAll();
            } else {
                setIsErrorCreateUpadate(true);
                setMessageErrorCreateUpdate(response.message);
            }
        }).catch((error) => {
            console.log(error);
        });
    }

    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.isOpenImportStock}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    Import Stock
                    {isLoading && <ProgressLinear />}
                    {isErrorCreateUpadate && <AlertText status={false} message={messageErrorCreateUpdate} />}
                </DialogTitle>
                <DialogContent>
                    <form onSubmit={handleSubmitUploadExcell}>
                        <Grid
                            container
                            spacing={2}
                            direction="column"
                            justifyContent="flex-start"
                            alignItems="stretch"
                            marginTop="5px"
                        >

                            <Grid item>
                                <input
                                    type={'file'}
                                    onChange={(e) => handleChangeFileUploadExcell(e)} />
                            </Grid>
                        </Grid>
                        <DialogActions>
                            <Button onClick={() => handleCloseAll()}>Cancel</Button>
                            <Button type="submit"> Save</Button>
                        </DialogActions>
                    </form>
                </DialogContent>


            </Dialog>
        </div>
    );

}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalImportUpdateStock);