import React from "react";
import PaidIcon from '@mui/icons-material/Paid';


export default function SpanStatusTransaction(props) {
    // if (props.status === 0) {
    //     return (
    //         <PaidIcon color="error" titleAccess="Belum dibayar"/>
    //     );
    // } else if (props.status === 1) {
    //     return (
    //         <PaidIcon color="success" titleAccess="Sudah dibayar"/>
    //     );
    // }
    if (props.status === 0) {
        return (
            <span style={{
                background: '#ff9b00d4',
                color: 'white',
                padding: '6px',
                borderRadius: '9px'
            }}>Belum dibayar</span>
        );
    } else if (props.status === 1) {
        return (
            <span style={{
                background: '#00d50ad4',
                color: 'white',
                padding: '6px',
                borderRadius: '9px'
            }}>Sudah dibayar</span>
        );
    } else if (props.status === 2) {
        return (
            <span style={{
                background: '#ff4040',
                color: 'white',
                padding: '6px',
                borderRadius: '9px'
            }}>Di cancel</span>
        );
    }else if (props.status === 99) {
        return (
            <span style={{
                background: '#ff87ed',
                color: 'white',
                padding: '6px',
                borderRadius: '9px'
            }}>Belum di Hitung</span>
        );
    }

}