import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert, getUserProfil } from "../../component/util/api/action";
import TruncateText from "../../component/template/TruncateText";

import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton, Grid } from "@mui/material";
import TablePagination from '@mui/material/TablePagination';
import HeaderTable from "../../component/template/HeaderTable";
import ModalDelete from "../../component/modal/ModalDelete";
import SpanStatusAdmin from "../../component/template/SpanStatusAdmin";
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import BlockIcon from '@mui/icons-material/Block';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import ModalCreateUpdateAdmin from "./ModalCreateUpdateAdmin";
import ModalSetPassword from "./ModalSetPassword";


const ListAdmin = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpenDelete, setIsOpenDelete] = useState(false);
    const [isOpenModalSetPassword, setIsOpenUpdatePassword] = useState(false);
    const [isFetching, setIsFetching] = useState(true);
    const [size, setSize] = useState(10);
    const [pages, setPages] = useState(0);
    const [totalPage, setTotalPage] = useState(0);
    const [totalItem, setTotalItem] = useState(0);
    const [itemSearch, setItemSearch] = useState('');

    const [listAdmin, setListAdmin] = useState([]);
    const [adminSelected, setAdminSelected] = useState({});
    const [idAdmin, setIdAdmin] = useState('');

    useEffect(() => {
        const getDatas = async () => {
            fetchListAdmin(size, pages, '');
            props.getUserProfil();
        };

        getDatas();

    }, []);

    const fetchListAdmin = async (size, pages, itemSearch) => {
        setIsFetching(true);

        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.ADMIN
            + "?size=" + size
            + "&page=" + pages
            + "&q=" + itemSearch
        ).then((response) => {
            if (response.status) {
                setPages(response.data.currentPage);
                setTotalPage(response.data.totalPages);
                setTotalItem(response.data.totalItems);
                setListAdmin(response.data.content);
            } else {
                setSize(10);
                setPages(0);
                setTotalPage(0);
                setTotalItem(0);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: response.message
                })
            }

            setIsFetching(false);
        }).catch((error) => {
            console.log(error);
        });
    }

    const handleSearch = async (search) => {
        setItemSearch(search);
        await fetchListAdmin(size, 0, search);
    }

    const handleOpen = () => {
        setIsOpen(!isOpen);
        if (isOpen) {
            setAdminSelected({});
        }
    }

    const handleDeleteOpen = async (admin) => {
        setAdminSelected(admin);
        setIdAdmin(admin.idAdmin);
        handleCloseDelete();
    }

    const handleCloseDelete = () => {
        setIsOpenDelete(!isOpenDelete);
    }

    const handleUpdate = (admin) => {
        setAdminSelected(admin);
        handleOpen();
    }

    const fetchListAdminFromAnother = async () => {
        await fetchListAdmin(size, pages, itemSearch);
    }

    const handleDoDelete = async () => {
        await CONFIG_BACKEND.delete(END_POINT_BACKEND_ONLINE_SHOP.ADMIN + "/" + idAdmin)
            .then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    fetchListAdminFromAnother();
                    handleCloseDelete();
                }
            })
            .catch((error) => {
                console.log(error);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
            });
    }

    const handleUpdateStatusAdmin = async (admin) => {
        await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.ADMIN_SUSPEND, {
            idAdmin: admin.idAdmin,
            statusAdmin: admin.statusAdmin == 1 ? 2 : 1
        } )
        .then((response) => {
            if (response.status) {
                props.handleAlert({
                    isOpen: true,
                    title: 'success',
                    message: response.message
                });
                fetchListAdminFromAnother();
            }
        })
        .catch((error) => {
            console.log(error);
            props.handleAlert({
                isOpen: true,
                title: 'error',
                message: error.message
            });
        });
    }

    const handleCloseModalSetPassword = (admin) => {
        setIsOpenUpdatePassword(!isOpenModalSetPassword);
        if (!isOpenModalSetPassword) {
            setAdminSelected(admin);
        }
    }

    const handleChangePage = async (e, newPage) => {
        setPages(newPage);
        await fetchListAdmin(size, newPage, itemSearch);
    }

    const handleChangeRowsPerPage = async (e) => {
        const sizeNow = parseInt(e.target.value, 10);
        setSize(sizeNow);
        setPages(0);
        await fetchListAdmin(sizeNow, pages, itemSearch);
    }

    return (
        <Fragment>
            <ModalCreateUpdateAdmin isOpen={isOpen} handleOpen={handleOpen} fetchListAdminFromAnother={fetchListAdminFromAnother} adminSelected={adminSelected} />
            <ModalSetPassword isOpenModalSetPassword={isOpenModalSetPassword} handleCloseModalSetPassword={handleCloseModalSetPassword} fetchListAdminFromAnother={fetchListAdminFromAnother}  adminSelected={adminSelected} />
            <ModalDelete isOpenDelete={isOpenDelete} handleCloseDelete={handleCloseDelete} nameOfObject={adminSelected.username} handleDoDelete={handleDoDelete} />
            <HeaderTable handleSearch={handleSearch} placeholder="Cari Admin" handleOpen={handleOpen} />

            {isFetching ? <LoadingSkeletonTable /> :
                <TableContainer component={Paper}>
                    <Table aria-label="collapsible table">
                        <TableHead>
                            <TableRow key="header">
                                <TableCell align="left">No</TableCell>
                                <TableCell align="left">Username</TableCell>
                                <TableCell align="left">Email</TableCell>
                                <TableCell align="center">Status</TableCell>
                                <TableCell align="center">#</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {listAdmin.length > 0 &&
                                listAdmin.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell align="left">{index + 1}</TableCell>
                                        <TableCell align="left">{row.username} {props.userProfil.idAdmin === row.idAdmin && ' (kamu)'} </TableCell>
                                        <TableCell align="left">{row.email}</TableCell>
                                        <TableCell align="center"><SpanStatusAdmin status={row.statusAdmin} /></TableCell>
                                        <TableCell align="center">
                                            <IconButton color="secondary" onClick={() => handleUpdate(row)} title={" Update " + row.username}>
                                                <EditOutlinedIcon />
                                            </IconButton >
                                            <IconButton color="error" onClick={() => handleDeleteOpen(row)} title={" Delete " + row.username} disabled={props.userProfil.idAdmin === row.idAdmin ? true : false} >
                                                <DeleteOutlineOutlinedIcon />
                                            </IconButton >
                                            <IconButton color="primary" onClick={() => handleCloseModalSetPassword(row)} title={" Update password " + row.username} disabled={props.userProfil.idAdmin === row.idAdmin ? true : false} >
                                                <LockOpenIcon />
                                            </IconButton >
                                            {
                                                row.statusAdmin === 1 ?
                                                    <IconButton color="warning" onClick={() => handleUpdateStatusAdmin(row)} title={" Nonaktifkan " + row.username} disabled={props.userProfil.idAdmin === row.idAdmin ? true : false} >
                                                        <BlockIcon />
                                                    </IconButton >
                                                    :
                                                    <IconButton color="success" onClick={() => handleUpdateStatusAdmin(row)} title={" Aktifkan " + row.username} disabled={props.userProfil.idAdmin === row.idAdmin ? true : false} >
                                                        <CheckCircleOutlineIcon />
                                                    </IconButton >
                                            }
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        labelRowsPerPage=""
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={totalItem}
                        rowsPerPage={size}
                        page={pages}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </TableContainer>
            }
        </Fragment>
    )

}

const mapStateToProps = state => {
    return {
        userProfil: state.userProfil
    }
}

export default connect(mapStateToProps, { handleAlert, getUserProfil })(ListAdmin);