import axios from "axios";
import history from "../history";
import { END_POINT_BACKEND_ONLINE_SHOP } from "./endpointBackendOnlineShop";

const tokenHeader = localStorage.getItem("token");
const token = tokenHeader !== null ? tokenHeader : "";
const instance = axios.create({
    baseURL: END_POINT_BACKEND_ONLINE_SHOP.ONLINE_SHOP_HOST,
    headers: {
        'Accept':  'application/json',
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + token
    }
});


instance.defaults.withCredentials = false;
instance.interceptors.response.use(response => {  
    console.groupCollapsed(" ============== Response Main ==============");
    console.info('token', token);
    console.info('status', response.status);
    console.info('data', response.data);
    console.info('full', response);
    console.groupEnd();

    if (response.status === 500) {
        const res = {
            status: false,
            message: "Internal Server Error",
            data: {}
        }
        return res;
    } else {
        return response.data;
    }
}, error => {
    console.groupCollapsed(" ============== Response Error ==============");
    console.info('error', error.response.data);
    console.groupEnd();

    return error.response.data;
    // localStorage.removeItem("user")
    // history.push('/login')
});

export default instance;