import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert, getUserProfil } from "../../component/util/api/action";


import LoadingSkeletonDetailTransaksi from "../../component/skeleton/LoadingSkeletonDetailTransaksi";
import SpanStatusTransaction from "../../component/template/SpanStatusTransaction";
import Stack from "@mui/material/Stack";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, Paper, Table, TableBody, TableCell, TableContainer, TableRow, TableHead, Typography } from "@mui/material";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const ModalDetailTransaction = (props) => {
    const [isFetching, setIsFetching] = useState(false);
    const [detailTransaction, setDetailTransaction] = useState([]);

    useEffect(() => {
        if (props.isOpen) {
            fetchDetailTransaction();
        }
    }, [props.isOpen]);


    const fetchDetailTransaction = async () => {
        setIsFetching(true);

        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.TRANSACTION_DETAIL_BY_ID_TRANSACTION + props.transactionSelected.idTransaction)
            .then((response) => {
                if (response.status) {
                    setDetailTransaction(response.data);
                } else {
                    props.handleAlert({
                        isOpen: true,
                        title: 'error',
                        message: response.message
                    })
                }

                setIsFetching(false);
            }).catch((error) => {
                console.log(error);
            });
    }


    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="lg"
                open={props.isOpen}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    <Typography variant="h6" component="div">Detail Transaksi</Typography>
                </DialogTitle>
                <DialogContent>
                    {isFetching ? <LoadingSkeletonDetailTransaksi />
                        :
                        <Fragment>
                            <Grid
                                container
                                direction="row"
                                justifyContent="space-between"
                                alignItems="flex-start"
                            >
                                <Grid item>
                                    {/* <Stack spacing={1}> */}
                                    <Typography variant="h6" component="div">Kode Transaksi: <b>{props.transactionSelected.transactionCode}</b></Typography>
                                    <Typography variant="body1" component="div">Tanggal: {props.transactionSelected.createdAt}</Typography>
                                    {/* </Stack> */}
                                </Grid>
                                <Grid item>
                                    {/* <Stack spacing={1}> */}
                                    <Typography variant="body1" component="div"><SpanStatusTransaction status={props.transactionSelected.transactionStatus} /></Typography>
                                    {/* </Stack> */}
                                </Grid>
                            </Grid>
                            <TableContainer component={Paper}>
                                <Table size="small" arial-label="collapsible table">
                                    <TableHead>
                                        <TableRow key={0}>
                                            <TableCell align="left">No</TableCell>
                                            <TableCell align="left">Produk</TableCell>
                                            <TableCell align="left">Jumlah</TableCell>
                                            <TableCell align="left">@Harga</TableCell>
                                            <TableCell align="left">Discount</TableCell>
                                            <TableCell align="left">Total</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {detailTransaction &&
                                            detailTransaction.map((row, index) =>
                                                <TableRow key={index + 1}>
                                                    <TableCell>{index + 1}</TableCell>
                                                    <TableCell>{row.product.productName}</TableCell>
                                                    <TableCell>{row.amount}</TableCell>
                                                    <TableCell>{row.priceString}</TableCell>
                                                    <TableCell>{row.discount}%</TableCell>
                                                    <TableCell>{row.totalPriceString}</TableCell>
                                                </TableRow>
                                            )
                                        }
                                        <TableRow key={-1}>
                                            <TableCell align="right" colSpan={5}>
                                                <b>Total Bayar</b>
                                            </TableCell>
                                            <TableCell align="left">
                                                <b>{props.transactionSelected.totalPayString}</b>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>

                                </Table>
                            </TableContainer>
                        </Fragment>
                    }

                    <DialogActions>
                        <Button onClick={() => props.handleOpen()}>Cancel</Button>
                    </DialogActions>
                </DialogContent>


            </Dialog>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalDetailTransaction);