import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert, getUserProfil } from "../../component/util/api/action";
import TruncateText from "../../component/template/TruncateText";

import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton, Checkbox } from "@mui/material";
import TablePagination from "@mui/material/TablePagination";
import HeaderTable from "../../component/template/HeaderTable";
import ModalDelete from "../../component/modal/ModalDelete";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import DeleteOutlineOutlinedIcon from "@mui/icons-material/DeleteOutlineOutlined";
import FormatListNumberedIcon from '@mui/icons-material/FormatListNumbered';
import ModalCreateUpdateInformationDetail from "./ModalCreateUpdateInformationDetail";

const ListInformationDetail = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpenDelete, setIsOpenDelete] = useState(false);
    const [isFetching, setIsFetching] = useState(true);
    const [size, setSize] = useState(10);
    const [pages, setPages] = useState(0);
    const [totalPage, setTotalPage] = useState(0);
    const [totalItem, setTotalItem] = useState(0);
    const [itemSearch, setItemSearch] = useState("");

    const [listInformationDetail, setListInformationDetail] = useState([]);
    const [informationDetailSelected, setInformationDetailSelected] = useState({});
    const [idInformation, setIdInformation] = useState("");

    useEffect(() => {
        if (props.idInformation) {
            fetchListInformationDetail(size, pages, itemSearch);
        }

    }, [props.idInformation]);

    const fetchListInformationDetail = async (size, pages, itemSearch) => {
        setIsFetching(true);

        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION_DETAIL + "?size=" + size + "&page=" + pages + "&q=" + itemSearch + "&idInformation=" + props.idInformation)
            .then((response) => {
                if (response.status) {
                    setPages(response.data.currentPage);
                    setTotalPage(response.data.totalPages);
                    setTotalItem(response.data.totalItems);
                    setListInformationDetail(response.data.content);
                } else {
                    setListInformationDetail([]);
                    setSize(10);
                    setPages(0);
                    setTotalPage(0);
                    setTotalItem(0);
                    props.handleAlert({
                        isOpen: true,
                        title: "error",
                        message: response.message,
                    });
                }

                setIsFetching(false);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const handleSearch = async (search) => {
        setItemSearch(search);
        await fetchListInformationDetail(size, 0, search);
    };

    const handleOpen = () => {
        setIsOpen(!isOpen);
        if (isOpen) {
            setInformationDetailSelected({});
        }
    };

    const handleDeleteOpen = async (information) => {
        setInformationDetailSelected(information);
        setIdInformation(information.idInformation);
        handleCloseDelete();
    };

    const handleCloseDelete = () => {
        setIsOpenDelete(!isOpenDelete);
    };

    const handleUpdate = (information) => {
        setInformationDetailSelected(information);
        handleOpen();
    };

    const handleDoDelete = async () => {
        await CONFIG_BACKEND.delete(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION + "/" + idInformation)
            .then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    fetchListInformationDetailFromAnother();
                    handleCloseDelete();
                }
            })
            .catch((error) => {
                console.log(error);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
            });
    }

    const fetchListInformationDetailFromAnother = async () => {
        await fetchListInformationDetail(size, pages, itemSearch);
    };

    const handleChangePage = async (e, newPage) => {
        setPages(newPage);
        await fetchListInformationDetail(size, newPage, itemSearch);
    };

    const handleChangeRowsPerPage = async (e) => {
        const sizeNow = parseInt(e.target.value, 10);
        setSize(sizeNow);
        setPages(0);
        await fetchListInformationDetail(sizeNow, pages, itemSearch);
    };

    return (
        <Fragment>
            <ModalCreateUpdateInformationDetail isOpen={isOpen} handleOpen={handleOpen} fetchListInformationDetailFromAnother={fetchListInformationDetailFromAnother} informationDetailSelected={informationDetailSelected} />
            <ModalDelete isOpenDelete={isOpenDelete} handleCloseDelete={handleCloseDelete} nameOfObject={informationDetailSelected.detailInformationTitle} handleDoDelete={handleDoDelete} />
            <HeaderTable handleSearch={handleSearch} placeholder="Cari Informasi" handleOpen={handleOpen} />

            {isFetching ? (
                <LoadingSkeletonTable />
            ) : (
                <TableContainer component={Paper}>
                    <Table size="small" aria-label="collapsible table">
                        <TableHead>
                            <TableRow key="header">
                                <TableCell align="left">No.</TableCell>
                                <TableCell align="left">Sub Judul</TableCell>
                                <TableCell align="left">Deskripsi</TableCell>
                                <TableCell align="center">#</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {listInformationDetail.length > 0 &&
                                listInformationDetail.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell align="left">{index + 1} </TableCell>
                                        <TableCell align="left">
                                            <TruncateText text={row.detailInformationTitle} />
                                        </TableCell>
                                        <TableCell align="left">
                                            <TruncateText text={row.detailInformationDescription} />
                                        </TableCell>
                                        <TableCell align="center">
                                            <IconButton color="secondary" onClick={() => handleUpdate(row)} title={" Update " + row.detailInformationTitle}>
                                                <EditOutlinedIcon />
                                            </IconButton>
                                            <IconButton color="error" onClick={() => handleDeleteOpen(row)} title={" Delete " + row.detailInformationTitle}>
                                                <DeleteOutlineOutlinedIcon />
                                            </IconButton>
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        labelRowsPerPage=""
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={totalItem}
                        rowsPerPage={size}
                        page={pages}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </TableContainer>
            )}
        </Fragment>
    );
};

const mapStateToProps = (state) => {
    return {
        userProfil: state.userProfil,
    };
};

export default connect(mapStateToProps, { handleAlert, getUserProfil })(ListInformationDetail);
