import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import idx from "indonesia-numbering-formatter";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton, Grid } from "@mui/material";
import TablePagination from '@mui/material/TablePagination';
import HeaderTable from "../../component/template/HeaderTable";
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import LocalGasStationIcon from '@mui/icons-material/LocalGasStation';
import DownloadIcon from '@mui/icons-material/Download';
import UploadIcon from '@mui/icons-material/Upload';
import ModalDelete from "../../component/modal/ModalDelete";
import ModalCreateUpdateProduct from "./ModalCreateUpdateProduct"
import ModalUpdateStockProduct from "./ModalUpdateStockProduct"
import TruncateText from "../../component/template/TruncateText";
import DropdownCategory from "../category/DropdownCategory";
import ModalImportUpdateStock from "./ModalImportUpdateStock";

const ListProduct = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpenUpdateStock, setIsOpenUpdateStock] = useState(false);
    const [isOpenImportStock, setIsOpenImportStock] = useState(false);
    const [isOpenDelete, setIsOpenDelete] = useState(false);
    const [isFetching, setIsFetching] = useState(true);
    const [size, setSize] = useState(10);
    const [pages, setPages] = useState(0);
    const [totalPage, setTotalPage] = useState(0);
    const [totalItem, setTotalItem] = useState(0);
    const [itemSearch, setItemSearch] = useState('');

    const [productSelected, setProductSelected] = useState({});
    const [categorySelected, setCategorySelected] = useState({});
    const [idProduct, setIdProduct] = useState('');
    const [idCategory, setIdCategory] = useState('');
    const [listProduct, setListProduct] = useState([]);

    useEffect(() => {
        const getDatas = async () => {
            fetchListProduct(size, pages, idCategory, '');
        };

        getDatas();

    }, []);

    const fetchListProduct = async (size, pages, idCategory, itemSearch) => {
        setIsFetching(true);
        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.PRODUCT
            + "?size=" + size
            + "&page=" + pages
            + "&idCategory=" + idCategory
            + "&q=" + itemSearch
        ).then((response) => {
            if (response.status) {
                setPages(response.data.currentPage);
                setTotalPage(response.data.totalPages);
                setTotalItem(response.data.totalItems);
                setListProduct(response.data.content);

                // for handle category selected
                if (idCategory !== '') {
                    if (response.data.content.Length > 0) {
                        const category = response.data.content[0].categorie;
                        setCategorySelected(category);
                    } else {
                        setCategorySelected({});
                    }
                } else {
                    setCategorySelected({});
                }
            } else {
                setSize(10);
                setPages(0);
                setTotalPage(0);
                setTotalItem(0);
                setListProduct([]);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: response.message
                })
            }

            setIsFetching(false);
        }).catch((error) => {
            console.log(error);
        });
    }

    const handleSearch = async (search) => {
        setItemSearch(search);
        await fetchListProduct(size, 0, idCategory, search);
    }

    const handleOpen = () => {
        setIsOpen(!isOpen);
        if (isOpen) {
            setProductSelected({});
        }
    }

    const handleUpdate = (product) => {
        setProductSelected(product);
        handleOpen();
    }

    const handleOpenModalUpdateStock = (product) => {
        setIsOpenUpdateStock(!isOpenUpdateStock);
        if (product) {
            setProductSelected(product);
        }
    }

    const handleDeleteOpen = async (product) => {
        setProductSelected(product);
        setIdProduct(product.idProduct);
        handleCloseDelete();
    }

    const handleCloseDelete = () => {
        setIsOpenDelete(!isOpenDelete);
    }

    const handleChangePage = async (e, newPage) => {
        setPages(newPage);
        await fetchListProduct(size, newPage, idCategory, itemSearch);
    }

    const handleChangeRowsPerPage = async (e) => {
        const sizeNow = parseInt(e.target.value, 10);
        setSize(sizeNow);
        setPages(0);
        await fetchListProduct(sizeNow, pages, idCategory, itemSearch);
    }

    const fetchListProductFromAnother = async () => {
        await fetchListProduct(size, pages, idCategory, itemSearch);
    }

    const handleDoDelete = async () => {
        await CONFIG_BACKEND.delete(END_POINT_BACKEND_ONLINE_SHOP.PRODUCT + "/" + idProduct)
            .then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    fetchListProductFromAnother();
                    handleCloseDelete();
                }
            })
            .catch((error) => {
                console.log(error);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
            });
    }

    const handleChangeCategory = async (val) => {
        console.log(val)
        await fetchListProduct(size, pages, val, itemSearch)
        setIdCategory(val);
    }

    const handleOpenImportStock = async () => {
        setIsOpenImportStock(!isOpenImportStock);
    }

    return (
        <Fragment>
            <ModalImportUpdateStock isOpenImportStock={isOpenImportStock} handleOpenImportStock={handleOpenImportStock} fetchListProductFromAnother={fetchListProductFromAnother} />
            <ModalCreateUpdateProduct isOpen={isOpen} handleOpen={handleOpen} fetchListProductFromAnother={fetchListProductFromAnother} productSelected={productSelected} />
            <ModalUpdateStockProduct isOpenUpdateStock={isOpenUpdateStock} handleOpenModalUpdateStock={handleOpenModalUpdateStock} fetchListProductFromAnother={fetchListProductFromAnother} productSelected={productSelected} />
            <ModalDelete isOpenDelete={isOpenDelete} handleCloseDelete={handleCloseDelete} nameOfObject={productSelected.productName} handleDoDelete={handleDoDelete} />
            <HeaderTable handleSearch={handleSearch} placeholder="Cari Produk" handleOpen={handleOpen} />
            <Grid
                spacing={2}
                container
                direction="row"
                justifyContent="space-between"
                alignItems="flex-start"
                style={{ marginBottom: '10px' }}>
                <Grid item>
                    <DropdownCategory categorySelected={categorySelected} handleSetIdCategory={(val) => handleChangeCategory(val)} />
                </Grid>
                <Grid item>
                    <Grid container spacing={2}>
                        <Grid item>
                            <a href="/product-import-stock.xlsx" rel="noreferrer">
                                <IconButton arial-label="download template" title="Download Template">
                                    <DownloadIcon color="secondary" />
                                </IconButton>
                            </a>
                        </Grid>

                        <Grid item>
                            <IconButton arial-label="download template" title="Upload" onClick={() => handleOpenImportStock()}>
                                <UploadIcon color="success" />
                            </IconButton>
                        </Grid>

                    </Grid>
                </Grid>
            </Grid>
            {isFetching ? <LoadingSkeletonTable /> :
                <TableContainer component={Paper}>
                    <Table size="small" aria-label="collapsible table">
                        <TableHead>
                            <TableRow key="header">
                                <TableCell align="left">Kode Produk</TableCell>
                                <TableCell align="left">Foto</TableCell>
                                <TableCell align="left">Produk</TableCell>
                                <TableCell align="left">Kategori</TableCell>
                                <TableCell align="left">Deskripsi</TableCell>
                                <TableCell align="left">Harga</TableCell>
                                <TableCell align="left">Stok</TableCell>
                                <TableCell align="left">Discount</TableCell>
                                <TableCell align="center">#</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {listProduct.length > 0 &&
                                listProduct.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell align="left">{row.productCode}</TableCell>
                                        <TableCell align="left">
                                            <img src={row.productImages ? process.env.REACT_APP_HOST_URL_IMAGE + row.productImages : '/logo192.png'} title={row.productName + " | " + row.productDescription} width="70px" />
                                        </TableCell>
                                        <TableCell align="left">{row.productName}</TableCell>
                                        <TableCell align="left">{row.categorie.categoryName}</TableCell>
                                        <TableCell align="left">
                                            <TruncateText text={row.productDescription} />
                                        </TableCell>
                                        <TableCell align="left">Rp. {idx.format(row.price ? row.price : 0)}</TableCell>
                                        <TableCell align="left">{row.stock}</TableCell>
                                        <TableCell align="left">{row.discount}%</TableCell>
                                        <TableCell align="center">
                                            <IconButton color="primary" onClick={() => handleOpenModalUpdateStock(row)} title={" Update Stok " + row.productName}>
                                                <LocalGasStationIcon />
                                            </IconButton >
                                            <IconButton color="secondary" onClick={() => handleUpdate(row)} title={" Update " + row.productName}>
                                                <EditOutlinedIcon />
                                            </IconButton >
                                            <IconButton color="error" onClick={() => handleDeleteOpen(row)} title={" Delete " + row.productName}>
                                                <DeleteOutlineOutlinedIcon />
                                            </IconButton >
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        labelRowsPerPage=""
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={totalItem}
                        rowsPerPage={size}
                        page={pages}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </TableContainer>
            }
        </Fragment>
    )


}

const mapStateToProps = state => {
    return {
        detailProfil: state.detailProfil
    }
}

export default connect(mapStateToProps, { handleAlert })(ListProduct);