import React, { useEffect, useState, Fragment } from 'react';
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";

import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import CircularProgress from '@mui/material/CircularProgress';

function sleep(delay = 0) {
  return new Promise((resolve) => {
    setTimeout(resolve, delay);
  });
}

const DropdownCategory = (props) => {
  const [isFetching, setIsFetching] = useState(false);
  const [open, setOpen] = useState(false);
  const [options, setOptions] = useState([]);

  useEffect(() => {

  })

  const handleOnChange = async (value = null) => {
    setIsFetching(true);

    const categoryName = value ? '?categoryName=' + value : '';
    await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.CATEGORY_DROPDOWN + categoryName)
      .then((response) => {
        setIsFetching(false);
        if (response.status) {
          setOptions(response.data);
        } else {
          setOptions([]);
        }
      }).catch((error) => {
        console.log(error);
      })

  }

  const handleSetIdCategory = async (value) => {
    try {
      props.handleSetIdCategory(value.idCategory);
    } catch (error) {
      props.handleSetIdCategory('');
    }
  }

  return (
    <Autocomplete
      sx={{ width: 300 }}
      open={open}
      onOpen={() => {
        setOpen(true);
        handleOnChange('');
      }}
      onClose={() => {
        setOpen(false);
      }}
      defaultValue={props.categorySelected.idCategory ? props.categorySelected : null}
      isOptionEqualToValue={(option, value) => option.categoryName === value.categoryName}
      getOptionLabel={(option) => option.categoryName}
      options={options.length > 0 ? options :
        props.categorySelected.idCategory ? [props.categorySelected] : []}
      onChange={(event, value) => handleSetIdCategory(value)}
      loading={isFetching}
      renderInput={(params) => (
        <TextField
          {...params}
          label="Cari Kategori"
          size='small'
          onChange={(e) => handleOnChange(e.target.value)}
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <Fragment>
                {isFetching ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </Fragment>
            ),
          }}
        />
      )}
    />
  );
}

const mapStateToProps = (state) => {
  return {
    alertNotificationEntity: state.alertNotificationEntity
  }
}
export default connect(mapStateToProps)(DropdownCategory);