import React, { Component, Fragment } from "react";
import { Router, Route } from "react-router-dom";
import history from "./component/util/history";

import Home from "./page/home";
import About from "./page/about";

import Login from "./page/login";
import SendEmailForgotPassword from "./page/login/SendEmailForgotPassword";
import RenewPassword from "./page/login/RenewPassword";

import Category from "./page/category";
import Product from "./page/product";
import Payment from "./page/payment";

import Admin from "./page/admin";
import Member from "./page/member";

import Transaction from "./page/transaksi";
import Information from "./page/information";
import InformationDetail from "./page/detailnformation";

import Footer from "./component/main/footer";

class App extends Component {
  render() {
    return (
      <Fragment>
        <Router history={history}>
          <Route path="/" exact component={Login} />
          <Route path="/send-email-forgot-password" component={SendEmailForgotPassword} />
          <Route path="/reset-password" component={RenewPassword} />
          <Route path="/home" component={Home} />
          <Route path="/about" component={About} />
          <Route path="/category" component={Category} />
          <Route path="/product" component={Product} />
          <Route path="/payment" component={Payment} />
          <Route path="/transaction" component={Transaction} />
          <Route path="/information" component={Information} />
          <Route path="/informationDetail" component={InformationDetail} />

          {/* MGT - USER */}
          <Route path="/user-admin" component={Admin} />
          <Route path="/user-member" component={Member} />
        </Router>
        {/* <Footer /> */}
      </Fragment>
    );
  }
}

export default App;
