import React, { Fragment } from "react";
import Skeleton from '@mui/material/Skeleton';
import Stack from "@mui/material/Stack";
import { Grid } from "@mui/material";

const LoadingSkeletonDetailTransaksi = () => {

    return (
        <Fragment>
            {/* <Skeleton variant="text" animation='pulse' width="40%" height={60} />
            <Skeleton variant="text" animation='pulse' width="60%" height={40} />
            <Skeleton variant="text" animation='pulse' width="60%" height={20} />
            <br />
            {
                Array.from(new Array(4)).map((item, index) => (
                    <Skeleton key={index} variant='text' height={50} animation='pulse' />
                ))
            } */}
            <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
                <Grid item xs={10}>
                    <Skeleton variant="text" animation='pulse' width="60%" height={60} />
                    <Skeleton variant="text" animation='pulse' width="50%" height={40} />
                    <Skeleton variant="text" animation='pulse' width="50%" height={40} />
                </Grid>
                <Grid item xs={2}>
                    <Skeleton variant="text" animation='pulse' height={20} />
                </Grid>
                <Grid item xs={12}>
                    {
                        Array.from(new Array(4)).map((item, index) => (
                            <Skeleton key={index} variant='text' height={30} width={'' + Math.floor(Math.random() * 100) + '%'} animation='pulse' />
                        ))
                    }
                </Grid>
            </Grid>

        </Fragment>

    )
}

export default LoadingSkeletonDetailTransaksi;