import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert, getUserProfil } from "../../component/util/api/action";
import TruncateText from "../../component/template/TruncateText";


import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import SpanStatusTransaction from "../../component/template/SpanStatusTransaction";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton, Grid } from "@mui/material";
import TablePagination from '@mui/material/TablePagination';
import InfoIcon from '@mui/icons-material/Info';
import HeaderTable from "../../component/template/HeaderTable";
import ModalDetailTransaction from "./ModalDetailTransaction";

const ListTransaction = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpenDelete, setIsOpenDelete] = useState(false);
    const [isFetching, setIsFetching] = useState(true);
    const [size, setSize] = useState(10);
    const [pages, setPages] = useState(0);
    const [totalPage, setTotalPage] = useState(0);
    const [totalItem, setTotalItem] = useState(0);
    const [itemSearch, setItemSearch] = useState('');

    const [listTransaction, setLisTransaction] = useState([]);
    const [transactionSelected, setTransactionSelected] = useState({});
    const [idTransaction, setIdTransaction] = useState('');

    useEffect(() => {

        const getDatas = async () => {
            fetchListTransacktion(size, pages, '');
        };

        getDatas();

    }, []);

    const fetchListTransacktion = async (size, pages, itemSearch) => {
        setIsFetching(true);

        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.TRANSACTION
            + "?size=" + size
            + "&page=" + pages
            + "&q=" + itemSearch
        ).then((response) => {
            if (response.status) {
                setPages(response.data.currentPage);
                setTotalPage(response.data.totalPages);
                setTotalItem(response.data.totalItems);
                setLisTransaction(response.data.content);
            } else {
                setSize(10);
                setPages(0);
                setTotalPage(0);
                setTotalItem(0);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: response.message
                })
            }

            setIsFetching(false);
        }).catch((error) => {
            console.log(error);
        });
    }

    const handleSearch = async (search) => {
        setItemSearch(search);
        await fetchListTransacktion(size, 0, search);
    }

    const handleOpen = () => {
        setIsOpen(!isOpen);
    }

    const handleOpenDetail = (transaction) => {
        handleOpen();
        setTransactionSelected(transaction);
    }

    const handleChangePage = async (e, newPage) => {
        setPages(newPage);
        await fetchListTransacktion(size, newPage, itemSearch);
    }

    const handleChangeRowsPerPage = async (e) => {
        const sizeNow = parseInt(e.target.value, 10);
        setSize(sizeNow);
        setPages(0);
        await fetchListTransacktion(sizeNow, pages, itemSearch);
    }

    return (
        <Fragment>
            <ModalDetailTransaction isOpen={isOpen} handleOpen={handleOpen} transactionSelected={transactionSelected} />
            <HeaderTable hideButtonAdd={true} handleSearch={handleSearch} placeholder="Cari Transaksi" handleOpen={handleOpen} />


            {isFetching ? <LoadingSkeletonTable /> :
                <TableContainer component={Paper}>
                    <Table size="small" aria-label="collapsible table">
                        <TableHead>
                            <TableRow key="header">
                                <TableCell align="left">No</TableCell>
                                <TableCell align="left">Kode</TableCell>
                                <TableCell align="left">Tanggal</TableCell>
                                <TableCell align="left">Total Pay</TableCell>
                                <TableCell align="center">Paid</TableCell>
                                <TableCell align="center">#</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {listTransaction.length > 0 &&
                                listTransaction.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell align="left">{index + 1}</TableCell>
                                        <TableCell align="left">{row.transactionCode} </TableCell>
                                        <TableCell align="left">{row.createdAt}</TableCell>
                                        <TableCell align="left">{row.totalPayString}</TableCell>
                                        <TableCell align="center"><SpanStatusTransaction status={row.transactionStatus} /></TableCell>
                                        <TableCell align="center">
                                            <IconButton color="info" onClick={() => handleOpenDetail(row)} >
                                                <InfoIcon />
                                            </IconButton >
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        labelRowsPerPage=""
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={totalItem}
                        rowsPerPage={size}
                        page={pages}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </TableContainer>
            }
        </Fragment >
    )

}

const mapStateToProps = state => {
    return {
        userProfil: state.userProfil
    }
}

export default connect(mapStateToProps, { handleAlert, getUserProfil })(ListTransaction);