import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, TextField } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import DropdownCategory from "./DropdownCategory";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const ModalCreateUpdateCategory = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [categoryName, setCategoryName] = useState('');
    const [categoryDescription, setCategoryDescription] = useState('');

    useEffect(() => {

        const getDatas = async () => {
        };

        getDatas();

    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        const data = {
            categoryName: categoryName === '' ? props.categorySelected.categoryName : categoryName,
            categoryDescription: categoryDescription === '' ? props.categorySelected.categoryDescription : categoryDescription
        };

        if (!props.categorySelected.idCategory) {
            // create Category
            await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.CATEGORY, data
            ).then((response) => {
                props.handleAlert({
                    isOpen: true,
                    title: 'success',
                    message: response.message
                });
                handleCloseAll();
            }).catch((error) => {
                console.log(error);
            });
        } else {
            // update Category
            await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.CATEGORY + '/' + props.categorySelected.idCategory, data
            ).then((response) => {
                console.log(response);
                props.handleAlert({
                    isOpen: true,
                    title: 'success',
                    message: response.message
                });

                handleCloseAll();
            }).catch((error) => {
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
                console.log(error);
            });
        }
    }

    const handleCloseAll = () => {
        props.handleOpen();
        setIsLoading(false);
        props.fetchListCategoryFromAnother();
    }

    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.isOpen}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    {!props.categorySelected ? 'Tambah Kategori' : 'Edit Kategori'}
                    {isLoading && <ProgressLinear />}
                </DialogTitle>
                <DialogContent>
                    <form onSubmit={handleSubmit}>
                        <Grid
                            container
                            spacing={2}
                            direction="column"
                            justifyContent="flex-start"
                            alignItems="stretch"
                            marginTop="5px"
                        >
                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.categorySelected.categoryName}
                                    size="small"
                                    onChange={(e) => setCategoryName(e.target.value)}
                                    label="Nama Kategori"
                                    variant="outlined"
                                    required />
                            </Grid>

                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.categorySelected.categoryDescription}
                                    size="small"
                                    onChange={(e) => setCategoryDescription(e.target.value)}
                                    label="Deskripsi Kategori"
                                    variant="outlined"
                                    required />
                            </Grid>
                        </Grid>
                        <DialogActions>
                            <Button onClick={props.handleOpen}>Cancel</Button>
                            <Button type="submit"> Save</Button>
                        </DialogActions>
                    </form>
                </DialogContent>


            </Dialog>
        </div>
    );
}
const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalCreateUpdateCategory);