import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DateTimePicker } from "@mui/x-date-pickers/DateTimePicker";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import Slide from "@mui/material/Slide";
import Textarea from "@mui/joy/Textarea";
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, FormControlLabel, Checkbox } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import AlertText from "../../component/alert/AlertText";
import DropdownInformation from "../information/DropdownInformation";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const ModalCreateUpdateInformationDetail = (props) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isErrorCreateUpadate, setIsErrorCreateUpadate] = useState(false);
  const [messageErrorCreateUpdate, setMessageErrorCreateUpdate] = useState("");

  const [idInformation, setIdInformation] = useState("");
  const [detailInformationTitle, setDetailInformationTitle] = useState("");
  const [detailInformationDescription, setDetailInformationDescription] = useState("");


  useEffect(() => {
    if (props.isOpen) {
      console.log(props.informationDetailSelected)
      setDetailInformationTitle(props.informationDetailSelected.detailInformationTitle);
      setDetailInformationDescription(props.informationDetailSelected.detailInformationDescription);
      setIdInformation(props.informationDetailSelected.idInformation);
    }
  }, [props.isOpen]);

  const handleCancelModal = async () => {
    props.handleOpen();
    resetAll();
  };

  const handleCloseAll = () => {
    props.handleOpen();
    setIsLoading(false);
    resetAll();
    props.fetchListInformationDetailFromAnother();
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setIsErrorCreateUpadate(false);

    var data = {
      idInformation: idInformation,
      detailInformationTitle: detailInformationTitle,
      detailInformationDescription: detailInformationDescription
    };

    console.log(data);

    if (!props.informationDetailSelected.idDetailInformation) {
      // create Information Detail
      await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION_DETAIL, data)
        .then((response) => {
          if (response.status) {
            props.handleAlert({
              isOpen: true,
              title: "success",
              message: response.message,
            });
            handleCloseAll();
          } else {
            setIsLoading(false);
            setIsErrorCreateUpadate(true);
            setMessageErrorCreateUpdate(response.message);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      // update Information Detail
      await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.INFORMATION_DETAIL + "/" + props.informationDetailSelected.idDetailInformation, data)
        .then((response) => {
          if (response.status) {
            props.handleAlert({
              isOpen: true,
              title: "success",
              message: response.message,
            });
            handleCloseAll();
          } else {
            setIsLoading(false);
            setIsErrorCreateUpadate(true);
            setMessageErrorCreateUpdate(response.message);
          }
        })
        .catch((error) => {
          props.handleAlert({
            isOpen: true,
            title: "error",
            message: error.message,
          });
          console.log(error);
        });
    }
  };

  const resetAll = () => {
    setIsErrorCreateUpadate(false);
    setDetailInformationTitle('');
    setDetailInformationDescription('');
    setIdInformation('');
  };

  return (
    <div>
      <Dialog fullWidth={true} maxWidth="sm" open={props.isOpen} TransitionComponent={Transition}>
        <DialogTitle>
          {!props.informationDetailSelected.idDetailInformation ? "Tambah Detail Informasi" : "Edit Detail Informasi"}
          {isLoading && <ProgressLinear />}
          {isErrorCreateUpadate && <AlertText status={false} message={messageErrorCreateUpdate} />}
        </DialogTitle>
        <DialogContent>
          <form onSubmit={handleSubmit}>
            <Grid container spacing={2} direction="column" justifyContent="flex-start" alignItems="stretch" marginTop="5px">
              <Grid item>
                <DropdownInformation
                  informationDetailSelected={props.informationDetailSelected.information ? props.informationDetailSelected.information : {}}
                  handleSetIdInformation={(idInformation) => setIdInformation(idInformation)}
                />
              </Grid>
              <Grid item>
                <TextField
                  fullWidth
                  value={detailInformationTitle}
                  size="small"
                  onChange={(e) => setDetailInformationTitle(e.target.value)}
                  label="Sub Informasi"
                  variant="outlined"
                  required
                />
              </Grid>
              <Grid item>
                <Textarea placeholder="Deskripsi Sub Informasi" value={detailInformationDescription} onChange={(e) => setDetailInformationDescription(e.target.value)} minRows={2} maxRows={4} required />
              </Grid>
            </Grid>
            <DialogActions>
              <Button onClick={handleCancelModal}>Cancel</Button>
              <Button type="submit"> Save</Button>
            </DialogActions>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    alertNotificationEntity: state.alertNotificationEntity,
  };
};
export default connect(mapStateToProps, { handleAlert })(ModalCreateUpdateInformationDetail);
