import React, { Component } from "react";
import { connect } from 'react-redux';
import history from "../../component/util/history";

import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { checkToken, handleLoginRedux, handleLoginError } from "../../component/util/api/action";
import AlertNotification from "../../component/alert/alertNotification";
import AlertText from "../../component/alert/AlertText";
import ProgressLinear from "../../component/template/ProgressLinear";

class SendEmailForgotPassword extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isLoading: false,
            showAlert: false,
            token: null,
        }
    }

    handleChange = async (e) => {
        const id = e.target.id;
        const value = e.target.value;
        this.setState({ [id]: value });
    }

    componentDidMount() {
        const queryParams = new URLSearchParams(this.props.location.search);
        this.setState({ token: queryParams.get('token') })

    }

    handleRenewPassword = async (e) => {
        e.preventDefault();
        const { token, password1, password2 } = this.state;

        if (password1 !== password2) {

            var objectAlertNotificationEntity = {
                isOpen: true,
                title: 'Salah !!',
                message: 'Ulangi password, password tidak sesuai',
            }

            this.props.handleLoginError(objectAlertNotificationEntity);

            return;

        }

        this.setState({
            isLoading: true
        });

        // excute reset password
        await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.FORGOT_PASSWORD_RENEW_PASSWORD, {
            tokenResetPassword: token,
            newPassword: password1,
        }).then(response => {

            if (response.status) {
                this.setState({
                    isLoading: false,
                    showAlert: true,
                    message: response.message,
                });

                setTimeout(() => {
                    history.push("/");
                }, 3000);
            } else {
                this.setState({ isLoading: false });
                objectAlertNotificationEntity = {
                    isOpen: true,
                    title: 'Error',
                    message: response.message,
                }

                this.props.handleLoginError(objectAlertNotificationEntity);
            }
        }).catch(error => {
            console.info('response error', error);
        });
    }

    render() {
        const { isLoading, showAlert, message } = this.state;
        return (
            <main>
                <div className="container">

                    <section className="section register min-vh-100 d-flex flex-column align-items-center justify-content-center py-4">
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-lg-4 col-md-6 d-flex flex-column align-items-center justify-content-center">

                                    <div className="d-flex justify-content-center py-4">
                                        <AlertNotification />
                                        {showAlert && <AlertText status={'success'} message={message} />}
                                    </div>

                                    <div className="card mb-3">

                                        <div className="card-body">

                                            <div className="pt-4 pb-2">
                                                {isLoading && <ProgressLinear />}
                                                <p className="text-center small">Atur ulang password kamu</p>
                                            </div>

                                            <form className="row g-3 needs-validation" onSubmit={this.handleRenewPassword}>

                                                <div className="col-12">
                                                    <label className="form-label">Password</label>
                                                    <div className="input-group has-validation">
                                                        <input type="password" name="password1" className="form-control" id="password1" required onChange={this.handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-12">
                                                    <label className="form-label">Ulangi Password</label>
                                                    <div className="input-group has-validation">
                                                        <input type="password" name="password2" className="form-control" id="password2" required onChange={this.handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-12">
                                                    <br />
                                                </div>
                                                <div className="col-12">
                                                    <button className="btn btn-primary w-100" type="submit">Kirim</button>
                                                </div>
                                            </form>

                                        </div>
                                    </div>

                                    <div className="credits">
                                        {process.env.REACT_APP_MODE}
                                    </div>

                                </div>
                            </div>
                        </div>

                    </section>

                </div>

            </main>
        );
    }
}

const mapStateToProps = state => {
    return {
        detailProfil: state.detailProfil,
        alertNotificationEntity: state.alertNotificationEntity
    }
}

export default connect(mapStateToProps, { checkToken, handleLoginRedux, handleLoginError })(SendEmailForgotPassword);