import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton } from "@mui/material";
import TablePagination from '@mui/material/TablePagination';
import HeaderTable from "../../component/template/HeaderTable";
import ModalCreateUpdateCategory from "./ModalCreateUpdateCategory";
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import ModalDelete from "../../component/modal/ModalDelete";

const ListCategory = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpenDelete, setIsOpenDelete] = useState(false);
    const [isFetching, setIsFetching] = useState(true);
    const [size, setSize] = useState(10);
    const [pages, setPages] = useState(0);
    const [totalPage, setTotalPage] = useState(0);
    const [totalItem, setTotalItem] = useState(0);
    const [itemSearch, setItemSearch] = useState('');
    
    const [categorySelected, setCategorySelected] = useState({});
    const [idCategory, setIdCategory] = useState('');
    const [listCategory, setListCategory] = useState([]);

    useEffect(() => {
        const getDatas = async () => {
            fetchListCategory(size, pages, '');
        };

        getDatas();

    }, []);

    const fetchListCategory = async (size, pages, itemSearch) => {
        setIsFetching(true);
        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.CATEGORY
            + "?size=" + size
            + "&page=" + pages
            + "&q=" + itemSearch
        ).then((response) => {
            if (response.status) {
                setPages(response.data.currentPage);
                setTotalPage(response.data.totalPages);
                setTotalItem(response.data.totalItems);
                setListCategory(response.data.content);
            } else {
                setSize(10);
                setPages(0);
                setTotalPage(0);
                setTotalItem(0);
                setListCategory([]);
            }

            setIsFetching(false);
        }).catch((error) => {
            console.log(error);
        });
    }

    const fetchListCategoryFromAnother = async () => {
        await fetchListCategory(size, pages, itemSearch);
    }

    const handleSearch = async (search) => {
        setItemSearch(search);
        await fetchListCategory(size, 0, search);
    }

    const handleDeleteOpen = async (category) => {
        setCategorySelected(category);
        setIdCategory(category.idCategory);
        handleCloseDelete();
    }

    const handleCloseDelete = () => {
        setIsOpenDelete(!isOpenDelete);
    }

    const handleOpen = () => {
        setIsOpen(!isOpen);
        if (isOpen) {
            setCategorySelected({});
        }
    }

    const handleUpdate = (category) => {
        setCategorySelected(category);
        handleOpen();
    }

    const handleDoDelete = async () => {
        await CONFIG_BACKEND.delete(END_POINT_BACKEND_ONLINE_SHOP.CATEGORY + "/" + idCategory)
            .then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    fetchListCategoryFromAnother();
                    handleCloseDelete();
                }
            })
            .catch((error) => {
                console.log(error);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
            });
    }

    const handleChangePage = async (e, newPage) => {
        setPages(newPage);
        await fetchListCategory(size, newPage, itemSearch);
    }

    const handleChangeRowsPerPage = async (e) => {
        const sizeNow = parseInt(e.target.value, 10);
        setSize(sizeNow);
        setPages(0);
        await fetchListCategory(sizeNow, pages, itemSearch);
    }


    return (
        <Fragment>
            <ModalCreateUpdateCategory isOpen={isOpen} handleOpen={handleOpen} fetchListCategoryFromAnother={fetchListCategoryFromAnother} categorySelected={categorySelected} />
            <ModalDelete isOpenDelete={isOpenDelete} handleCloseDelete={handleCloseDelete} nameOfObject={categorySelected.categoryName} handleDoDelete={handleDoDelete} />
            <HeaderTable handleSearch={handleSearch} placeholder="Cari Kategori" handleOpen={handleOpen} />
            {isFetching ? <LoadingSkeletonTable /> :
                <TableContainer component={Paper}>
                    <Table size="small" aria-label="collapsible table">
                        <TableHead>
                            <TableRow>
                                <TableCell align="left">No</TableCell>
                                <TableCell align="left">Nama Kategori</TableCell>
                                <TableCell align="left">Deskripsi</TableCell>
                                <TableCell />
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {listCategory.length > 0 &&
                                listCategory.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell align="left">{index + 1}</TableCell>
                                        <TableCell align="left">{row.categoryName}</TableCell>
                                        <TableCell align="left">{row.categoryDescription}</TableCell>
                                        <TableCell align="left">
                                            <IconButton color="secondary" onClick={() => handleUpdate(row)}>
                                                <EditOutlinedIcon />
                                            </IconButton >
                                            <IconButton color="error" onClick={() => handleDeleteOpen(row)}>
                                                <DeleteOutlineOutlinedIcon />
                                            </IconButton >
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        labelRowsPerPage=""
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={totalItem}
                        rowsPerPage={size}
                        page={pages}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </TableContainer>
            }
        </Fragment>
    )
}

const mapStateToProps = state => {
    return {
        detailProfil: state.detailProfil
    }
}

export default connect(mapStateToProps, { handleAlert })(ListCategory);