import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import CONFIG_BACKEND_MULTIPART_FORM from "../../component/util/api/configBackendOnlineShopMultipartForm";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, Typography } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import DropdownCategory from "../category/DropdownCategory.js";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


const ModalUpdateStockProduct = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [idCategory, setIdCategory] = useState(null);
    const [stock, setStock] = useState(0);

    const handleCloseAll = () => {
        props.handleOpenModalUpdateStock();
        setIsLoading(false);
        props.fetchListProductFromAnother();
    }

    const handleCancelModal = () => {
        props.handleOpenModalUpdateStock();
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        var data = {
            stock: stock === 0 ? props.productSelected.stock : stock,
        };

        if (props.productSelected.idProduct && stock >= 0) {
            // create Product
            await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.PRODUCT_UPDATE_STOCK + props.productSelected.idProduct, data
            ).then((response) => {
                props.handleAlert({
                    isOpen: true,
                    title: 'success',
                    message: response.message
                });
                handleCloseAll();
            }).catch((error) => {
                console.log(error);
            });
        } else {
            props.handleAlert({
                isOpen: true,
                title: 'error',
                message: 'Periksa kembali inputan anda'
            });
            setIsLoading(false);
        }

    }



    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.isOpenUpdateStock}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    Update Stok '{props.productSelected.productName}'
                    {isLoading && <ProgressLinear />}
                </DialogTitle>
                <DialogContent>
                    <form onSubmit={handleSubmit}>
                        <Grid
                            container
                            spacing={2}
                            direction="column"
                            justifyContent="flex-start"
                            alignItems="stretch"
                        >
                            <Grid item>
                                <Typography variant="body1" component="div" gutterBottom>Stok sekarang {props.productSelected.stock}</Typography>
                            </Grid>
                            <Grid item>
                                <TextField
                                    fullWidth
                                    size="small"
                                    type="number"
                                    onChange={(e) => setStock(e.target.value)}
                                    label="Stok Produk baru"
                                    variant="outlined"
                                    required />
                            </Grid>
                        </Grid>
                        <DialogActions>
                            <Button onClick={handleCancelModal}>Cancel</Button>
                            <Button type="submit"> Save</Button>
                        </DialogActions>
                    </form>
                </DialogContent>


            </Dialog>
        </div>
    );

}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalUpdateStockProduct);