import React, { Component, Fragment } from "react";
import Header from "../../component/main/header";
import Sidebar from "../../component/main/sidebar";
import Main from "../../component/main/main";
import { connect } from "react-redux";

import ListInformationDetail from "./ListInformationDetail";

class index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            idInformation: ''
        };
    }

    async componentDidMount() {
        const queryParams = new URLSearchParams(this.props.location.search);
        this.setState({ idInformation: queryParams.get('idInformation') })
        console.log(queryParams.get('idInformation'));
    }

    render() {
        const { idInformation } = this.state;
        return (
            <Fragment>
                <Header />
                <Sidebar />
                <main id="main" className="main">
                    <Main title="Detail Informasi" />
                    <ListInformationDetail idInformation={idInformation} />
                </main>
            </Fragment>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        detailProfil: state.detailProfil,
        modalError: state.modalError,
    };
};
export default connect(mapStateToProps)(index);
