import React, { Component, Fragment } from "react";
import { checkToken } from "../../component/util/api/action";
import Header from '../../component/main/header';
import Sidebar from '../../component/main/sidebar';
import Main from '../../component/main/main';
import { connect } from 'react-redux';
import actionType from "../../redux/reducer/globalActionType";
import ModalError from "../../component/modal/modalError";

class index extends Component {

    componentDidMount = async () => {        
        this.props.checkToken();
        const data = {
                isOpen: true,
                title: "Error gaeessss",
                message: "this message gaesss"
        }

        // setTimeout(() => {
        //     this.props.handleModalError(data);
        // }, 2000);
    }

    render() {
        return (
            <Fragment>
                <Header />
                <Sidebar />
                <main id="main" className="main">
                    <Main title="Home" />
                    {/* <ModalError modal={ this.props.modalError } /> */}

                </main>
            </Fragment>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        modalError: state.modalError
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        handleModalError: (payload) => dispatch({ type: actionType.MODAL_ERROR, modalError: payload })
    }
}
export default connect(mapStateToProps, {checkToken, mapDispatchToProps})(index);