import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import CONFIG_BACKEND_MULTIPART_FORM from "../../component/util/api/configBackendOnlineShopMultipartForm";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, Typography } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


const ModalSetPassword = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [idAdmin, setIdAdmin] = useState(null);
    const [newPassword, setNewPassword] = useState(null);

    const handleCloseAll = () => {
        props.handleCloseModalSetPassword();
        setIsLoading(false);
        props.fetchListProductFromAnother();
    }

    const handleCancelModal = () => {
        props.handleCloseModalSetPassword();
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        var data = {
            password: newPassword
        };

        console.log(props.adminSelected)

        if (props.adminSelected.idAdmin && newPassword !== '') {
            // create Product
            await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.ADMIN+'/' + props.adminSelected.idAdmin, data
            ).then((response) => {
                props.handleAlert({
                    isOpen: true,
                    title: 'success',
                    message: response.message
                });
                handleCloseAll();
            }).catch((error) => {
                console.log(error);
            });
        } else {
            props.handleAlert({
                isOpen: true,
                title: 'error',
                message: 'Periksa kembali inputan anda'
            });
            setIsLoading(false);
        }

    }

    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.isOpenModalSetPassword}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    Ganti password '{props.adminSelected.username}'
                    {isLoading && <ProgressLinear />}
                </DialogTitle>
                <DialogContent>
                    <form onSubmit={handleSubmit}>
                        <Grid
                            container
                            spacing={2}
                            direction="column"
                            justifyContent="flex-start"
                            alignItems="stretch"
                            marginTop="5px"
                        >
                            <Grid item>
                                <TextField
                                    fullWidth
                                    size="small"
                                    type="text"
                                    onChange={(e) => setNewPassword(e.target.value)}
                                    label="Password Baru"
                                    variant="outlined"
                                    required />
                            </Grid>
                        </Grid>
                        <DialogActions>
                            <Button onClick={handleCancelModal}>Cancel</Button>
                            <Button type="submit"> Save</Button>
                        </DialogActions>
                    </form>
                </DialogContent>


            </Dialog>
        </div>
    );

}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalSetPassword);