import React from "react";
import { connect } from "react-redux";
import Alert from "@mui/material/Alert";
import Stack from "@mui/material/Stack";

import { handleAlert } from "../util/api/action";

const AllAlertNotification = (props) => {
  const handleClose = async () => {
    const alert = {
      isOpen: false,
      title: null,
      message: null,
    };
    await props.handleAlert(alert);
  };
  return (
    <Stack sx={{ width: "100%" }} spacing={2}>
      {props.alertNotificationEntity.isOpen && (
        <Alert severity={props.alertNotificationEntity.title} onClose={() => handleClose()}>
          {props.alertNotificationEntity.message}
        </Alert>
      )}
    </Stack>
  );
};

const mapStateToProps = (state) => {
  return {
    alertNotificationEntity: state.alertNotificationEntity,
  };
};

export default connect(mapStateToProps, { handleAlert })(AllAlertNotification);
