import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import TruncateText from "../../component/template/TruncateText";

import LoadingSkeletonTable from "../../component/skeleton/LoadingSkeletonTable";
import { Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, IconButton, Grid } from "@mui/material";
import TablePagination from '@mui/material/TablePagination';
import HeaderTable from "../../component/template/HeaderTable";
import ModalDelete from "../../component/modal/ModalDelete";
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import ModalCreateUpdatePayment from "./ModalCreateUpdatePayment";


const ListPayment = (props) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpenDelete, setIsOpenDelete] = useState(false);
    const [isFetching, setIsFetching] = useState(true);
    const [size, setSize] = useState(10);
    const [pages, setPages] = useState(0);
    const [totalPage, setTotalPage] = useState(0);
    const [totalItem, setTotalItem] = useState(0);
    const [itemSearch, setItemSearch] = useState('');

    const [listPayment, setListPayment] = useState([]);
    const [paymentSelected, setSelectedPayment] = useState({});
    const [idPayment, setIdPayment] = useState('');

    useEffect(() => {
        const getDatas = async () => {
            fetchListPayment(size, pages, '');
        };

        getDatas();

    }, []);

    const fetchListPayment = async (size, pages, itemSearch) => {
        setIsFetching(true);

        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.PAYMENT
            + "?size=" + size
            + "&page=" + pages
            + "&q=" + itemSearch
        ).then((response) => {
            if (response.status) {
                setPages(response.data.currentPage);
                setTotalPage(response.data.totalPages);
                setTotalItem(response.data.totalItems);
                setListPayment(response.data.content);
            } else {
                setSize(10);
                setPages(0);
                setTotalPage(0);
                setTotalItem(0);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: response.message
                })
            }

            setIsFetching(false);
        }).catch((error) => {
            console.log(error);
        });
    }

    const handleSearch = async (search) => {
        setItemSearch(search);
        await fetchListPayment(size, 0, search);
    }

    const handleOpen = () => {
        setIsOpen(!isOpen);
        if (isOpen) {
            setSelectedPayment({});
        }
    }

    const handleDeleteOpen = async (payment) => {
        setSelectedPayment(payment);
        setIdPayment(payment.idPayment);
        handleCloseDelete();
    }

    const handleCloseDelete = () => {
        setIsOpenDelete(!isOpenDelete);
    }

    const handleUpdate = (payment) => {
        setSelectedPayment(payment);
        handleOpen();
    }

    const fetchListPaymentFromAnother = async () => {
        await fetchListPayment(size, pages, itemSearch);
    }

    const handleDoDelete = async () => {
        await CONFIG_BACKEND.delete(END_POINT_BACKEND_ONLINE_SHOP.PAYMENT + "/" + idPayment)
            .then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    fetchListPaymentFromAnother();
                    handleCloseDelete();
                }
            })
            .catch((error) => {
                console.log(error);
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
            });
    }

    const handleChangePage = async (e, newPage) => {
        setPages(newPage);
        await fetchListPayment(size, newPage, itemSearch);
    }

    const handleChangeRowsPerPage = async (e) => {
        const sizeNow = parseInt(e.target.value, 10);
        setSize(sizeNow);
        setPages(0);
        await fetchListPayment(sizeNow, pages, itemSearch);
    }

    return (
        <Fragment>
            <ModalCreateUpdatePayment isOpen={isOpen} handleOpen={handleOpen} fetchListPaymentFromAnother={fetchListPaymentFromAnother} paymentSelected={paymentSelected} />
            <ModalDelete isOpenDelete={isOpenDelete} handleCloseDelete={handleCloseDelete} nameOfObject={paymentSelected.paymentName} handleDoDelete={handleDoDelete} />
           <HeaderTable handleSearch={handleSearch} placeholder="Cari Pembayaran" handleOpen={handleOpen} />

            {isFetching ? <LoadingSkeletonTable /> :
                <TableContainer component={Paper}>
                    <Table size="small" aria-label="collapsible table">
                        <TableHead>
                            <TableRow key="header">
                                <TableCell align="left">No</TableCell>
                                <TableCell align="left">Kode Pemb</TableCell>
                                <TableCell align="left">Nama Pemb</TableCell>
                                <TableCell align="left">Deskripsi</TableCell>
                                <TableCell align="center">#</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {listPayment.length > 0 &&
                                listPayment.map((row, index) => (
                                    <TableRow key={index}>
                                        <TableCell align="left">{index + 1}</TableCell>
                                        <TableCell align="left">{row.paymentCode}</TableCell>
                                        <TableCell align="left">{row.paymentName}</TableCell>
                                        <TableCell align="left">
                                            <TruncateText text={row.paymentDescription} />
                                        </TableCell>
                                        <TableCell align="center">
                                            <IconButton color="secondary" onClick={() => handleUpdate(row)} title={" Update " + row.productName}>
                                                <EditOutlinedIcon />
                                            </IconButton >
                                            <IconButton color="error" onClick={() => handleDeleteOpen(row)} title={" Delete " + row.productName}>
                                                <DeleteOutlineOutlinedIcon />
                                            </IconButton >
                                        </TableCell>
                                    </TableRow>
                                ))}
                        </TableBody>
                    </Table>
                    <TablePagination
                        labelRowsPerPage=""
                        rowsPerPageOptions={[5, 10, 25]}
                        component="div"
                        count={totalItem}
                        rowsPerPage={size}
                        page={pages}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </TableContainer>
            }
        </Fragment>
    )
}


const mapStateToProps = state => {
    return {
        detailProfil: state.detailProfil
    }
}

export default connect(mapStateToProps, { handleAlert })(ListPayment);