import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, Typography } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const ModalDelete = (props) => {

    return (
        <Dialog
            fullWidth={true}
            maxWidth="sm"
            open={props.isOpenDelete}
            TransitionComponent={Transition}
        >
            <DialogTitle>
                <Typography variant="h5" component="div" sx={{ flexGrow: 1 }}></Typography>
            </DialogTitle>
            <DialogContent>
                <Grid container
                    spacing={2}
                    direction="column"
                    justifyContent="flex-start"
                    alignItems="center">
                    <Grid item xs={12} style={{ textAlign: 'center' }}>
                        <img src="/delete.png" alt="delete" style={{ width: '10%' }} />
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h5" component="div" sx={{ flexGrow: 1 }}>Apakah anda yakin ingin menghapus data ini?</Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} style={{ color: 'red' }}>{props.nameOfObject}</Typography>
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button variant="outlined" color="primary" onClick={() => props.handleCloseDelete()}>Cancel</Button>
                <Button variant="outlined" color="error" onClick={() => props.handleDoDelete()}>Delete</Button>
            </DialogActions>
        </Dialog>
    );
}

export default ModalDelete;