import history from "../history";
import actionType from "../../../redux/reducer/globalActionType";
import CONFIG_BACKEND from "../../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../../component/util/api/endpointBackendOnlineShop";
import { ContactSupport } from "@mui/icons-material";


export const signOut = () => async (dispatch) => {
    localStorage.removeItem("token");
    localStorage.removeItem("userProfil");

    setTimeout(() => {
        // history.go(0);
        history.push("/");
    }, 1000)
}

export const getUserProfil = () => async (dispatch) => {
    const token = localStorage.getItem("token");
    const userProfil = localStorage.getItem("userProfil");
    if (token && userProfil) {
        dispatch({
            type: actionType.USER_PROFIL,
            userProfil: JSON.parse(userProfil)
        })
    } else {
        // setTimeout(() => {
        //     history.push("/");
        // },1000)
    }
}


export const checkToken = () => async (dispatch) => {
    const token = localStorage.getItem("token");
    if (token) {
        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.CHECK_TOKEN)
            .then((response) => {
                console.info("CHECKTOKEN success", response);
                if (response.status) {
                    history.push("/home");
                } else {
                    history.push("/");
                }
            })
            .catch((error) => {
                console.error("CHECKTOKEN error", error);
                localStorage.removeItem("token");
                localStorage.removeItem("userProfil");
                window.location("/");
            });
    } else {
        console.info("CHECKTOKEN no token");
    }
}

export const handleLoginRedux = (payload) => async (dispatch) => {
    dispatch({ type: actionType.USER_PROFIL, userProfil: payload })
}

export const handleLoginError = (payload) => async (dispatch) => {
    dispatch({ type: actionType.ALERT_ACTION, alertNotificationEntity: payload })
}

export const handleAlert = (payload) => async (dispatch) => {
    dispatch({ type: actionType.ALERT_ACTION, alertNotificationEntity: payload })
    setTimeout(() => {
        dispatch({ type: actionType.ALERT_ACTION, alertNotificationEntity: { isOpen: false } })
    }, 5000);
}
