import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, Typography } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import AlertText from "../../component/alert/AlertText";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const ModalCreateUpdatePayment = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [isErrorCreateUpadate, setIsErrorCreateUpadate] = useState(false);
    const [messageErrorCreateUpdate, setMessageErrorCreateUpdate] = useState('');
    const [idPayment, setIdPayment] = useState(null);
    const [paymentName, setPaymentName] = useState('');
    const [paymentDescription, setPaymentDescription] = useState('');

    const handleCancelModal = async () => {
        props.handleOpen();
        resetAll();
    }

    const handleCloseAll = () => {
        props.handleOpen();
        setIsLoading(false);
        resetAll();
        props.fetchListPaymentFromAnother();
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);

        var data = {
            idPayment: idPayment === null ? props.paymentSelected.idPayment : idPayment,
            paymentName: paymentName === '' ? props.paymentSelected.paymentName : paymentName,
            paymentDescription: paymentDescription === '' ? props.paymentSelected.paymentDescription : paymentDescription
        };

        if (!props.paymentSelected.idPayment) {
            // create Payment
            await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.PAYMENT, data
            ).then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    handleCloseAll();
                } else {
                    setIsLoading(false);
                    setIsErrorCreateUpadate(true);
                    setMessageErrorCreateUpdate(response.message);
                }
            }).catch((error) => {
                console.log(error);
            });
        } else {
            // update Product
            await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.PAYMENT + '/' + props.paymentSelected.idPayment, data
            ).then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    handleCloseAll();
                } else {
                    setIsLoading(false);
                    setIsErrorCreateUpadate(true);
                    setMessageErrorCreateUpdate(response.message);
                }

            }).catch((error) => {
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
                console.log(error);
            });
        }
    }

    const resetAll = () => {
        setIsLoading(false);
        setIdPayment(null);
        setPaymentName('');
        setPaymentDescription('');
    }

    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.isOpen}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    {!props.paymentSelected.idPayment ? 'Tambah Payment' : 'Edit Payment'}
                    {isLoading && <ProgressLinear />}
                    {isErrorCreateUpadate && <AlertText status={false} message={messageErrorCreateUpdate} />}
                </DialogTitle>
                <DialogContent>
                    <form onSubmit={handleSubmit}>
                        <Grid
                            container
                            spacing={2}
                            direction="column"
                            justifyContent="flex-start"
                            alignItems="stretch"
                            marginTop="5px"
                        >
                            {props.paymentSelected.idPayment &&
                                <Grid item>
                                    <Typography variant="subtitle1" color="initial">Kode Pembayaran : <b>{props.paymentSelected.paymentCode}</b></Typography>
                                </Grid>
                            }

                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.paymentSelected.paymentName}
                                    size="small"
                                    onChange={(e) => setPaymentName(e.target.value)}
                                    label="Nama Pembayaran"
                                    variant="outlined"
                                    required />
                            </Grid>

                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.paymentSelected.paymentDescription}
                                    size="small"
                                    onChange={(e) => setPaymentDescription(e.target.value)}
                                    label="Deskripsi Pembayaran"
                                    variant="outlined"
                                    required />
                            </Grid>

                        </Grid>
                        <DialogActions>
                            <Button onClick={handleCancelModal}>Cancel</Button>
                            <Button type="submit"> Save</Button>
                        </DialogActions>
                    </form>
                </DialogContent>


            </Dialog>
        </div>
    );


}


const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalCreateUpdatePayment);