import React, { Component } from "react";
import { connect } from 'react-redux';
import history from "../../component/util/history";

import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { checkToken, handleLoginRedux, handleLoginError } from "../../component/util/api/action";
import AlertNotification from "../../component/alert/alertNotification";
import AlertText from "../../component/alert/AlertText";
import ProgressLinear from "../../component/template/ProgressLinear";

class SendEmailForgotPassword extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isLoading: false,
            showAlert: false,
            email: null,
        }
    }

    componentDidMount() {
    }

    handleChange = async (e) => {
        const id = e.target.id;
        const value = e.target.value;
        this.setState({ [id]: value });
    }

    handleSendEmail = async (e) => {
        e.preventDefault();
        this.setState({
            isLoading: true
        });
        const { email } = this.state;

        var objectAlertNotificationEntity = {
            isOpen: false,
            title: '',
            message: '',
        }

        this.props.handleLoginError(objectAlertNotificationEntity);

        // excute send email
        await CONFIG_BACKEND.get(END_POINT_BACKEND_ONLINE_SHOP.FORGOT_PASSWORD_SEND_EMAIL + email).then(response => {
            console.groupCollapsed(" ============== Response SEND EMAIL ==============");
            console.info('response status', response.status);
            console.info('response data', response.data);
            console.groupEnd();

            if (response.status) {
                this.setState({
                    isLoading: false,
                    showAlert: true,
                    message: response.message,
                });

                setTimeout(() => {
                    history.push("/");
                }, 3000);
            } else {
                this.setState({ isLoading: false });
                objectAlertNotificationEntity = {
                    isOpen: true,
                    title: 'Error',
                    message: response.message,
                }

                this.props.handleLoginError(objectAlertNotificationEntity);
            }
        }).catch(error => {
            console.info('response error', error);
        });
    }

    render() {
        const { isLoading, showAlert, message } = this.state;
        return (
            <main>
                <div className="container">

                    <section className="section register min-vh-100 d-flex flex-column align-items-center justify-content-center py-4">
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-lg-4 col-md-6 d-flex flex-column align-items-center justify-content-center">

                                    <div className="d-flex justify-content-center py-4">
                                        <AlertNotification />
                                        {showAlert && <AlertText status={'success'} message={message} />}
                                    </div>

                                    <div className="card mb-3">

                                        <div className="card-body">

                                            <div className="pt-4 pb-2">
                                                {isLoading && <ProgressLinear />}
                                                <h5 className="card-title text-center pb-0 fs-4">Lupa Password</h5>
                                                <p className="text-center small">Masukkan email kamu, untuk melakukan reset password kamu</p>
                                            </div>

                                            <form className="row g-3 needs-validation" onSubmit={this.handleSendEmail}>

                                                <div className="col-12">
                                                    <label className="form-label">Email</label>
                                                    <div className="input-group has-validation">
                                                        <span className="input-group-text" id="inputGroupPrepend">@</span>
                                                        <input type="email" name="email" className="form-control" id="email" required onChange={this.handleChange} />
                                                    </div>
                                                </div>
                                                <div className="col-12">
                                                    <br />
                                                </div>
                                                <div className="col-12">
                                                    <button className="btn btn-primary w-100" type="submit">Kirim</button>
                                                </div>
                                            </form>

                                        </div>
                                    </div>

                                    <div className="credits">
                                        {process.env.REACT_APP_MODE}
                                    </div>

                                </div>
                            </div>
                        </div>

                    </section>

                </div>

            </main>
        );
    }
}

const mapStateToProps = state => {
    return {
        detailProfil: state.detailProfil,
        alertNotificationEntity: state.alertNotificationEntity
    }
}

export default connect(mapStateToProps, { checkToken, handleLoginRedux, handleLoginError })(SendEmailForgotPassword);