import React from "react";
import TextTruncate from 'react-text-truncate';

export default function TruncateText(props) {
    return (
        <TextTruncate
            line={1}
            element="span"
            truncateText=" "
            text={props.text}
            textTruncateChild={<a href="javascript:void(0)" title={props.text}>...</a>}
        />
    );
}