import React, { Fragment, useEffect, useState } from "react";
import { connect } from "react-redux";
import CONFIG_BACKEND from "../../component/util/api/configBackendOnlineShop";
import { END_POINT_BACKEND_ONLINE_SHOP } from "../../component/util/api/endpointBackendOnlineShop";
import { handleAlert } from "../../component/util/api/action";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Slide from '@mui/material/Slide';
import { DialogActions, DialogContent, DialogTitle, Grid, TextField, Typography } from "@mui/material";
import ProgressLinear from "../../component/template/ProgressLinear";
import AlertText from "../../component/alert/AlertText";


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const ModalCreateUpdateAdmin = (props) => {
    const [isLoading, setIsLoading] = useState(false);
    const [isErrorCreateUpadate, setIsErrorCreateUpadate] = useState(false);
    const [messageErrorCreateUpdate, setMessageErrorCreateUpdate] = useState('');
    const [idAdmin, setIdAdmin] = useState(null);
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');

    const handleCancelModal = async () => {
        props.handleOpen();
        resetAll();
    }

    const handleCloseAll = () => {
        props.handleOpen();
        setIsLoading(false);
        resetAll();
        props.fetchListAdminFromAnother();
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setIsLoading(true);
        setIsErrorCreateUpadate(false);

        var data = {
            idAdmin: idAdmin === null ? props.adminSelected.idAdmin : idAdmin,
            username: username === null ? props.adminSelected.username : username,
            email: email === null ? props.adminSelected.email : email,
        };

        if (!props.adminSelected.idAdmin) {
            // create Admin
            await CONFIG_BACKEND.post(END_POINT_BACKEND_ONLINE_SHOP.ADMIN, data
            ).then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    handleCloseAll();
                } else {
                    setIsLoading(false);
                    setIsErrorCreateUpadate(true);
                    setMessageErrorCreateUpdate(response.message);
                }
            }).catch((error) => {
                console.log(error);
            });
        } else {
            // update Admin
            await CONFIG_BACKEND.put(END_POINT_BACKEND_ONLINE_SHOP.ADMIN + '/' + props.adminSelected.idAdmin, data
            ).then((response) => {
                if (response.status) {
                    props.handleAlert({
                        isOpen: true,
                        title: 'success',
                        message: response.message
                    });
                    handleCloseAll();
                } else {
                    setIsLoading(false);
                    setIsErrorCreateUpadate(true);
                    setMessageErrorCreateUpdate(response.message);
                }
            }).catch((error) => {
                props.handleAlert({
                    isOpen: true,
                    title: 'error',
                    message: error.message
                });
                console.log(error);
            });
        }
    }

    const resetAll = () => {
        setIsErrorCreateUpadate(false);
        setIdAdmin(null);
        setUsername('');
        setEmail('');
    }

    return (
        <div>
            <Dialog
                fullWidth={true}
                maxWidth="sm"
                open={props.isOpen}
                TransitionComponent={Transition}
            >
                <DialogTitle>
                    {!props.adminSelected.idAdmin ? 'Tambah Admin' : 'Edit Admin'}
                    {isLoading && <ProgressLinear />}
                    {isErrorCreateUpadate && <AlertText status={false} message={messageErrorCreateUpdate} />}
                </DialogTitle>
                <DialogContent>
                    <form onSubmit={handleSubmit}>
                        <Grid
                            container
                            spacing={2}
                            direction="column"
                            justifyContent="flex-start"
                            alignItems="stretch"
                            marginTop="5px"
                        >
                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.adminSelected.username}
                                    size="small"
                                    onChange={(e) => setUsername(e.target.value)}
                                    label="Username"
                                    variant="outlined"
                                    disabled={props.adminSelected.idAdmin ? true : false}
                                    required />
                            </Grid>

                            <Grid item>
                                <TextField
                                    fullWidth
                                    defaultValue={props.adminSelected.email}
                                    size="small"
                                    onChange={(e) => setEmail(e.target.value)}
                                    type="email"
                                    label="Email"
                                    variant="outlined"
                                    required />
                            </Grid>

                        </Grid>
                        <DialogActions>
                            <Button onClick={handleCancelModal}>Cancel</Button>
                            <Button type="submit"> Save</Button>
                        </DialogActions>
                    </form>
                </DialogContent>


            </Dialog>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        alertNotificationEntity: state.alertNotificationEntity
    }
}
export default connect(mapStateToProps, { handleAlert })(ModalCreateUpdateAdmin);