import actionType from "./globalActionType";
import AlertNotificationEntity from "../../component/entity/alertNotificationEntity";
import ModalErrorEntity from "../../component/entity/modalErrorEntity";
import history from "../../component/util/history";



const initState = {
    test: null,
    userProfil: {},
    modalErrorEntity: ModalErrorEntity,
    alertNotificationEntity: AlertNotificationEntity,
    tokenIsValid: false,
}

// Reducer
const rootReducer = (state = initState, action) => {
    console.groupCollapsed(" ============== root reducer ==============");
    console.info('root reducer - state', state);
    console.info('root reducer - action', action);
    console.groupEnd();

    switch (action.type) {
        case actionType.USER_PROFIL:
            state = {
                ...state,
                userProfil: action.userProfil
            }
            break;

        case actionType.MODAL_ERROR:
            state = {
                ...state,
                modalErrorEntity: action.ModalErrorEntity
            }
            break;

        case actionType.CHECK_TOKEN:
            state = {
                ...state,
                tokenIsValid: true
            }
            break;

        case actionType.ALERT_ACTION:
            state = {
                ...state,
                alertNotificationEntity: action.alertNotificationEntity
            }
            break;

        case actionType.TEST_ACTION:
            state = {
                ...state,
                test: action.test
            }
            break;

        default:
            break;
    }

    return state;
}


export default rootReducer;